        <i  class="fa fa-eye"></i>                  
        </button>                   
        
        <button  v-on:click="$parent.$parent.newPage(item)" class="btn waves-effect waves-light btn-primary btn-icon">
          <i  class="fa fa-tv"></i>                  
        </button>                  
         <button  v-on:click="$parent.$parent.downloadexcell(item)" class="btn waves-effect waves-light btn-primary btn-icon">
                             <i  class="fa ">E</i>                  </button>                                     
        <button  v-on:click="$parent.$parent.goto(item)" class="btn waves-effect waves-light btn-primary btn-icon">                    
