<script>
export default {
  name:"textEditor",
  props:{ 
    "value":{default:''}
  },
  mounted:function() {
    this.loadEditor()
    
  },
  methods: {
     loadEditor:function (){ 
      var Size = Quill.import('attributors/style/size');
      Size.whitelist = ['12px', '16px', '18px', '20px', '26px', '32px', '40px']; 
      Quill.register(Size, true);

      var toolbarOptions = [
        [
          'bold', 'italic', 'underline', 'strike',
           { 'color': [] }, { 'background': [] },
           { 'size': ['10px', '12px', '16px', '20px', '26px'] }, 
           { 'direction': 'rtl' }
        ],        
      ];
      var el=document.getElementById("editorArea")
      console.log('element is :',el)
      if(!el)
      {
        this.editorLoaded=false
        return 
      }
      if(this.editorLoaded)
      {
        return
      }
      el.innerHTML=this.value //this.tmpQue.text
      this.editorLoaded=true
      var editor = new Quill('#editorArea', {
        modules: { toolbar: '#announcementEditorToolbar' }, 
        theme: 'snow'
      });
      editor.format('direction', 'rtl');
      editor.format('align', 'right')
      var self=this
      editor.on('text-change',function(){
        self.$emit('input',editor.container.firstChild.innerHTML)
        // self.tmpQue.text = editor.container.firstChild.innerHTML;  
      })
    },
  },
}
</script>