<template>
<div >  
 <table >
     <tr >
            <td  style="border: 1px solid #ccc;border-radius: 3px;">
                    <div  v-if="data.type" style="margin: 10px 10;padding: 5px 20px 5px 40px; width: 90px">
                      
                        
                        <div  class="row">
                            <div  class="col-form-label"> 
                              <select  v-model="data.type" class="form-control">
                                  <option  value="and"> و </option>
                                  <option  value="or"> یا </option>
                              </select> 
                            </div>
                        </div>
                        
                        <div  class="row">
                            <button  v-on:click="addcond()" class="btn waves-effect waves-light btn-primary btn-icon">
                                <i  class="fa fa-plus"></i>
                            </button> 
                        </div>
                        <div  class="row">
                            <button  v-on:click="viewHide()" class="btn waves-effect waves-light btn-primary btn-icon">
                                <i  class="fa fa-eye"></i>
                            </button> 
                        </div>
                        <div  class="row">
                            <button  v-on:click="addsubcond()" class="btn waves-effect waves-light btn-primary btn-icon">
                                <i  class="fa fa-mail-reply"></i>
                            </button> 
                        </div>


                        <div  v-if="isQutaFunc()" class="row col-form-label"> 
                          سهمیه
                  
                      </div>
                      <div  v-if="isQutaFunc()" class="row">  
                              <input  class="form-control form-control-rtl" type="number" v-model="data.quota"/>
                      </div>
                        
                              
                  
                  
                    </div>
                  
                    <div  v-if="!data.type" style="margin: 10px 0;padding: 5px 10px 5px 40px;width: 700px;">
                     
                      <div  class="row">
                        <div  class="col-sm-2 col-form-label"> 
                          اگر در پرسشنامه 
                  
                        </div>
                        <div  class="col-md-4"> 
                            <select  v-model="data.questionnaireId" class="form-control" v-on:change="questionSelect($event)">
                                <option  v-for="(itm,index) in question " :key="itm.id"  v-bind:value="itm.id">
                                  {{itm.name}}
                                </option>
                            </select>
                   
                        </div>
                  
                        <div  class="col-sm-2 col-form-label"> 
                            و در سوال  
                  
                        </div>
                        <div  class="col-md-4"> 
                            <select  v-model="data.questionId" class="form-control" v-on:change="answChange($event)">
                                <option  v-for="(itm,index) in answers " :key="itm.id"  v-bind:value="itm.id">
                                  {{getText(itm.id +'-'+ convertToText(itm.text)) }}
                                </option>
                            </select>
                  
                   
                              
                        </div>
                            
                      </div>
                      <div  class="row"> 
                          <div  class="col-md-4" v-if="selectedAnsw.kind">
                              <div  v-if="selectedAnsw.kind[0]=='singleLine' || selectedAnsw.kind[0]=='multiLine'||selectedAnsw.kind[0]=='int' || selectedAnsw.kind[0]=='decimal'|| selectedAnsw.kind[0]=='range'|| selectedAnsw.kind[0]=='numberScale'|| selectedAnsw.kind[0]=='linearScale'"> 
                                   
                                  <div  v-if="selectedAnsw.answers && (selectedAnsw.answers.length+getexternamItems(selectedAnsw.external).length>0)">
                                        
                                      <select  v-model="data.kindof" class="form-control">
                                          <option  v-for="(itm,index) in selectedAnsw.answers " :key="itm.id+index"  v-bind:value="itm.id">
                                          {{itm.text}}
                                          
                                          
                                          </option> 
 
                                          <!---->
                                          <option  v-for="(itm,index) in getexternamItems(selectedAnsw.external)" :key="'xx'+index"  v-bind:value="itm.id">
                                            {{itm.text}}
                                          </option> 

                                      </select>
                                  </div>
                  
                              </div>
                          </div>
                          
                        <div  class="col-md-3">  
                            <select  v-model="data.operation" class="form-control" v-if="selectedAnsw.kind">
                                <option  v-for="(itm,index) in allOprByType[selectedAnsw.kind[0]] " :key="itm.id"  v-bind:value="itm.id">
                                  {{itm.title}}
                                </option>
                            </select>
                            
                        </div>
                        
                        
                        <div  class="col-md-4" v-if="selectedAnsw.kind"> 
                          <input  class="form-control form-control-rtl" v-if="selectedAnsw.kind[0]=='singleLine'" type="text" v-model="data.value"/>
                          <input  class="form-control form-control-rtl" v-if="selectedAnsw.kind[0]=='int' || selectedAnsw.kind[0]=='decimal'|| selectedAnsw.kind[0]=='range'|| selectedAnsw.kind[0]=='numberScale'|| selectedAnsw.kind[0]=='linearScale'" type="number" v-model="data.value"/>
                          <textarea  class="form-control form-control-rtl" v-if="selectedAnsw.kind[0]=='multiLine'" type="number" v-model="data.value">
                            </textarea>
                   
                          <select  v-if="selectedAnsw.kind[0]=='radio' || selectedAnsw.kind[0]=='check'  || selectedAnsw.kind[0]=='priority' " v-model="data.valueId" class="form-control">
                              <option  v-for="(itm,index) in tmpansw " :key="itm.id"  v-bind:value="itm.id">
                              {{itm.text}}
                              </option>
                          </select>
                        </div>  
                            
                      </div>
                    </div>
            </td>
            <td >
    <div  v-if="iscol">
    
    <div  v-if="isView &&  data.children && data.children.length" style="margin-right: 15px;">
       
      <div  v-for="(sc,index) in data.children " :key="sc.id" >
        <table >
            <tr >
                <td >
                    <i  class="fa fa-minus" style="float: right;padding-top: 30px;cursor: pointer;" v-on:click="deleteCond(sc.id,index)"></i>
                    <i  class="fa fa-clone" style="float: right;padding-top: 30px;cursor: pointer;" v-on:click="dplCond(sc.id)"></i>
                    
                </td>
                <td >
                    <condition  v-bind:isQuta="isQuta" v-bind:data="sc" v-bind:question="question"></condition>

                </td>
            </tr>
        </table>  

      </div>  
    

    </div>
  </div>
            </td>
     </tr>
 </table>   
    <span  style="display: none">
        {{forcheck}} 
    </span>


 

</div>
</template>

<script>
export default {
  name:"condition",
  data:function()
  {
    return {
      allOprByType:{"int":[{"id":"","title":"خالی"},{"id":"null","title":"بدون مقدار"},{"id":"notnull","title":"با مقدار"},{"id":"eq","title":"برابر"},{"id":"ne","title":"نا برابر"},{"id":"lt","title":"کوچکتر"},{"id":"lte","title":"کوچکتر مساوی"},{"id":"gt","title":"بزرگتر"},{"id":"gte","title":"بزرگتر مساوی"}],"decimal":[{"id":"","title":"خالی"},{"id":"null","title":"بدون مقدار"},{"id":"notnull","title":"با مقدار"},{"id":"eq","title":"برابر"},{"id":"ne","title":"نا برابر"},{"id":"lt","title":"کوچکتر"},{"id":"lte","title":"کوچکتر مساوی"},{"id":"gt","title":"بزرگتر"},{"id":"gte","title":"بزرگتر مساوی"}],"dateTime":[{"id":"","title":"خالی"},{"id":"null","title":"بدون مقدار"},{"id":"notnull","title":"با مقدار"},{"id":"eq","title":"برابر"},{"id":"ne","title":"نا برابر"},{"id":"lt","title":"کوچکتر"},{"id":"lte","title":"کوچکتر مساوی"},{"id":"gt","title":"بزرگتر"},{"id":"gte","title":"بزرگتر مساوی"}],"range":[{"id":"","title":"خالی"},{"id":"null","title":"بدون مقدار"},{"id":"notnull","title":"با مقدار"},{"id":"eq","title":"برابر"},{"id":"ne","title":"نا برابر"},{"id":"lt","title":"کوچکتر"},{"id":"lte","title":"کوچکتر مساوی"},{"id":"gt","title":"بزرگتر"},{"id":"gte","title":"بزرگتر مساوی"}],"linearScale":[{"id":"","title":"خالی"},{"id":"null","title":"بدون مقدار"},{"id":"notnull","title":"با مقدار"},{"id":"eq","title":"برابر"},{"id":"ne","title":"نا برابر"},{"id":"lt","title":"کوچکتر"},{"id":"lte","title":"کوچکتر مساوی"},{"id":"gt","title":"بزرگتر"},{"id":"gte","title":"بزرگتر مساوی"}],"numberScale":[{"id":"","title":"خالی"},{"id":"null","title":"بدون مقدار"},{"id":"notnull","title":"با مقدار"},{"id":"eq","title":"برابر"},{"id":"ne","title":"نا برابر"},{"id":"lt","title":"کوچکتر"},{"id":"lte","title":"کوچکتر مساوی"},{"id":"gt","title":"بزرگتر"},{"id":"gte","title":"بزرگتر مساوی"}],"singleLine":[{"id":"","title":"خالی"},{"id":"null","title":"بدون مقدار"},{"id":"notnull","title":"با مقدار"},{"id":"eq","title":"برابر"},{"id":"ne","title":"نا برابر"},{"id":"contain","title":"شامل"},{"id":"notcontain","title":"شامل نباشد"},{"id":"start","title":"شروع شود با"},{"id":"end","title":"خاتمه با"}],"multiLine":[{"id":"","title":"خالی"},{"id":"null","title":"بدون مقدار"},{"id":"notnull","title":"با مقدار"},{"id":"eq","title":"برابر"},{"id":"ne","title":"نا برابر"},{"id":"contain","title":"شامل"},{"id":"notcontain","title":"شامل نباشد"},{"id":"start","title":"شروع شود با"},{"id":"end","title":"خاتمه با"}],"radio":[{"id":"","title":"خالی"},{"id":"null","title":"بدون مقدار"},{"id":"notnull","title":"با مقدار"},{"id":"eq","title":"برابر"},{"id":"ne","title":"نا برابر"}],"priority":[{"id":"","title":"خالی"},{"id":"null","title":"بدون مقدار"},{"id":"notnull","title":"با مقدار"},{"id":"eq","title":"برابر"},{"id":"ne","title":"نا برابر"}],"check":[{"id":"","title":"خالی"},{"id":"null","title":"بدون مقدار"},{"id":"notnull","title":"با مقدار"},{"id":"eq","title":"برابر"},{"id":"ne","title":"نا برابر"}]},
      allOpr:[{"id":"","title":"خالی"},{"id":"eq","title":"برابر"},{"id":"ne","title":"نا برابر"},{"id":"lt","title":"کوچکتر"},{"id":"lte","title":"کوچکتر مساوی"},{"id":"gt","title":"بزرگتر"},{"id":"gte","title":"بزرگتر مساوی"}],
      isView:true,
      iscol:true,
      answers:[],
      selectedAnsw:{},
      forcheck:2,
      tmpansw:[],

    }
  },
  
  methods: { 
    getElementByRef:function(el){
      return this.$refs[el]
    },
    getMyParent:function(obj){return this.$parent},
    setState:function(obj){
      var al={"question":true,"myAnsw":true,"data":true,"isRoot":true,"isQuta":true}
      if(obj)
        for(var a in obj)
        {
          //if(al[a])
          //  continue
          try{
            this[a]=obj[a] 
          }catch(exp){}
        }
        
    },
    getexternamItems:function(datax){
      var lret=[]
      for(var b of datax)
      { 
        for(var a of this.question)
        { 
          if(a.id==this.data.questionnaireId)
          { 
            for(var q of a.questions)
            {
              if(q.id==b.id)
              {
                console.log('xq------------>1',q)
                for(var answ of q.answers)
                {
                  lret.push({id:q.id+'_'+answ.id,text:answ.text})

                }
              }
            }
            break;
          }
        } 
      }
      return lret;
    },
    isQutaFunc:function (){
      console.log()
      if(!this.isQuta   )
        return false
      if(!this.data.children)
        return true  
      for(var a of this.data.children)
      {
        if(a.type)
          return false
      }
      return true
    },
    deleteCond:function (id,index){
      console.log('',id)
      if(typeof(id)=="undefined")
      {
        this.data.children.splice(index,1)

        return
      }
      for(var a=0;a<this.data.children.length;a++)
      {
        if(this.data.children[a].id==id)
        {
          this.data.children.splice(a,1)
           break
        }
      } 
          this.forcheck++
      this.setState({})
    },
    viewHide:function (){
      this.isView=!this.isView
    },
    dplCond:function (id){
      console.log(id)
      for(var a=0;a<this.data.children.length;a++)
      {
        if(this.data.children[a].id==id)
        {
          this.data.children.splice(a,0,getCopy(this.data.children[a]))
           break
        }
      } 
          this.forcheck++
      this.setState({})
    },
    addcond:function (){
      if(!this.data.children)
        this.data.children=[]
      this.data.children.push(
        {  
          "id":guid(),
          "questionnaireId":global.thisQuestionary,
          "questionId":"",
          "operation":"eq",
          "value":""
      }
      )  
      this.forcheck++
      this.setState({data:this.data})
    },
    addsubcond:function (){
      if(!this.data.children)
        this.data.children=[]
      this.data.children.push(
        {  
          "id":guid(),
         "type":"and"
      }
      ) 
      this.forcheck++
      this.setState({data:this.data})

    },
    convertToText:function (data){
      return $(data).text()
    },
    getText:function (text){
      if(text.length>80)
        text=text.substr(0,80)+'...'
        return text
    },
    reloadQuestion:function (question){
      var questionAnsw={}
      for(var a of question.questions)
      {
        var n=""+a.id
        questionAnsw[n]={}
        for(var b of a.answers)
        {
          questionAnsw[n][a.id+'_'+b.id] ={id:a.id+'_'+b.id,q:a.id,a:b.id,text:b.text,expected:b.expected}
        }
        if(a.external)
        { 
            for(var j of a.external)
            {
              var i = questionAnsw[j.id+""] 
                for(var xx in i)
                {
                  var x=i[xx]
                  var canp=false  
                    questionAnsw[n][ x.id]={id: x.id,q:x.q,a:x.id,text:x.text}
                } 
            } 
        }
         
      } 
      console.log('questionAnsw-------> ',questionAnsw)
      return questionAnsw
    },
    loadAnsw:function (val){
      console.log('loadAnsw----->',val)
      for(var a of this.answers)
      {
        if(a.id==val)
        {
          this.selectedAnsw=a
          console.log('ansChane',this.selectedAnsw)

         // var tmp=this.reloadQuestion()// global.answ[this.selectedAnsw.id]
          this.tmpansw=[]
          //this.selectedAnsw.answers
          for(var j in this.tempQuestion[a.id])
          {
            var tx=this.tempQuestion[a.id][j]
            this.tmpansw.push({text:tx.text ,id:tx.a})
          }
          console.log('ansChane', this.tmpansw )
        }
      }

    },
    loadQuest:function (data){
      console.log('loadQuest----->',data)
      for(var a of this.question)
      { 
        if(a.id==data)
        {
          this.answers =  a.questions
          this.tempQuestion=this.reloadQuestion(a)
          
          console.log('loadQuest----->',this.answers)
          this.setState({})
          return
        }
      } 
    },
    answChange:function (data){
      //console.log('ansChane',)
      var val=data.target.value
      this.data.value=""
      this.data.operation="eq"
      this.loadAnsw(val)
 
    },
    questionSelect:function (data){ 
      this.data.questionId=""
      this.selectedAnsw={}
      this.data.value=""
      this.data.operation=""

      var val=data.target.value

      this.loadQuest(val)
    },
    setIsCol:function (isc){
      this.setState({iscol:isc})
    },
    getByType:function (){
      if(this.data.type=="and")
      {
        return "و"
      }
      if(this.data.type=="or")
      {
        return "یا" 
      }
    },
        
    },
    
    mounted:function (){ 
      if(this.data.questionnaireId)
      {
        this.loadQuest(this.data.questionnaireId)
      }
      if(this.data.questionId)
      {
        this.loadAnsw(this.data.questionId)
      }
      console.log('xxxxxxxxxxxxxxxxxxxxx ',this.isQuta) 

    },

    props:{
        'question':{default:function(){ return []}},
        'myAnsw':{default:function(){ return {}}},
        'data':{default:function(){ return {}}},
        'isRoot':{default:false},
        'isQuta':{default:false},
        
      }, 
}
</script>