<template>
  <div  class="modal" id="editModal" tabindex="-1" role="dialog">
      <div  class="modal-dialog" role="document">
          <div  class="modal-content">
          <div  class="modal-header">
          <h4  class="modal-title">انتخاب سوال ها </h4>
          <button  type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span  aria-hidden="true">&times;</span>
          </button>
          </div>
              <div  class="modal-body">   
                <div  class="card-block"  > 
                  <div  v-for="(que,index) in tempExt " :key="que.id"  class="  card-sub qitem" style="padding: 10px" v-bind:tagid="que.id">
                     
                    
                     <div  class="row">
                         <div  class="col-md-12"> 
                           <p  class="card-text">{{  (index+1) + ' - '+ convertToText(que.text)}}</p>
                         </div>
                         <div  class="col-md-4">
                            <select  v-if="!que.isAdded" v-model="que.type" class="form-control">
                                <option  value="all"> همه </option>
                                <option  value="selected"> انتخاب شده </option>
                                <option  value="notSelected"> انتخاب نشده </option>
                            </select>  
                            <span  v-if="que.isAdded" class="form-control form-control-rtl">
                                {{selectToName(que.type)}}
                            </span>
                         </div> 
                         <div  class="col-md-8">
                             <button v-if="isAdded(que.id)" v-on:click="deleteFromList(que)" class="btn waves-effect waves-light btn-danger btn-icon">
                               <i  class="fa fa-times"></i>
                             </button> 





                             <button  v-if="!isAdded(que.id)" v-on:click="addToList(que)" class="btn waves-effect waves-light btn-primary btn-icon">
                               <i  class="fa fa-plus"></i>
                             </button>
                         </div>  
                     </div>      
                  </div>
  
                
                
                </div>
              </div>
          <div  class="modal-footer">
          <button  type="button" class="btn btn-default waves-effect " data-dismiss="modal">خروج</button> 
          </div>
          </div>
      </div>
   </div>
</template>

<script>
export default {
  name:"relationModal",
  props:{
    tempExt:{},
    tmpQue:{}
  },
  data:function(){
    return {
      //tempExt:[]
    };
  },
  mounted() {
    //this.tempExt= global1.questionPage.tempExt
  },
  methods:{ 
    isAdded(id)
    {
        if(this.tmpQue.external)
        {
          for(var a of this.tmpQue.external)
          {
            if(a.id==id)return true
          }
        }
        return false
    },
    addToList:function (q)
    {  
      console.log('sssssssssssssss',q)
      if(!this.tmpQue.external)
        this.tmpQue.external=[]
      this.tmpQue.external.push({id:q.id,type:q.type,text:q.text, }) 
      this.$emit("save",this.tmpQue)
    },
    deleteFromList:function (q)
    {
      for(var a=0;a<this.tmpQue.external.length;a++)
      {
        if(this.tmpQue.external[a].id==q.id)
        {
          this.tmpQue.external.splice(a,1)
          break
        }
      } 
      this.$emit("save",this.tmpQue)
    },
    convertToText:function(data){
      return $(data).text()
    },
    selectToName:function (name){
      if(name=="all")
      {
        return "همه"
      }
      if(name=="selected")
      {
        return "انتخاب شده " 
      }
      if(name=="notSelected")
      {
        return "انتخاب نشده" 
      }
      return ""
    },
  },

}
</script>