<template>
<section  class="login-block">
    
    <div  class="container-fluid">
        <div  class="row">
            <div  class="col-sm-12">
                
                <div  class="md-float-material form-material">
                    <div  class="text-center">
                        <img  src="files/assets/images/logo.png" alt="logo.png"></img>
                    </div>
                    <div  class="auth-box card">
                        <div  class="card-block">
                            <div  class="row m-b-20">
                                <div  class="col-md-12">
                                    <h3  class="text-center txt-primary">ایجاد حساب کاربری</h3>
                                </div>
                            </div>
                            
                            <p  class="text-muted text-center p-b-5"> ایجاد حساب کاربری عمومی</p>
                            <div  class="form-group form-primary">
                                <input  v-model="username" type="text" name="user-name" class="form-control" required=""/>
                                <span  class="form-bar"></span>
                                <label  class="float-label">شماره موبایل </label>
                            </div>
                            
                            <div  class="row">
                                <div  class="col-sm-6">
                                    <div  class="form-group form-primary">
                                        <input  v-model="password" type="password" name="password" class="form-control" required=""/>
                                        <span  class="form-bar"></span>
                                        <label  class="float-label">رمز عبور</label>
                                    </div>
                                </div>
                                <div  class="col-sm-6">
                                    <div  class="form-group form-primary">
                                        <input  v-model="rePassword" type="password" name="confirm-password" class="form-control" required=""/>
                                        <span  class="form-bar"></span>
                                        <label  class="float-label">تکرار رمز عبور</label>
                                    </div>
                                </div>
                            </div>
                            <div  class="form-group form-primary">
                                <input  v-model="ref" type="text" name="user-name" class="form-control" required=""/>
                                <span  class="form-bar"></span>
                                <label  class="float-label">کد معرف </label>
                            </div>
                            <div  class="row m-t-25 text-left">
                                <div  class="col-md-12">
                                    <div  class="checkbox-fade fade-in-primary">
                                        <label >
                                            <input  v-model="isRule" type="checkbox" value=""/>
                                            <span  class="cr"><i  class="cr-icon icofont icofont-ui-check txt-primary"></i></span>
                                            <span  class="text-inverse">  <a  href="">قوانین</a> را قبول دارم</span>
                                        </label>
                                    </div>
                                </div>
                                
                            </div>
                            <div  class="row m-t-30">
                                <div  class="col-md-12">
                                    <button  v-on:click="register()" class="btn btn-primary btn-md btn-block waves-effect text-center m-b-20">ایجاد حساب</button>
                                </div>
                            </div>
                            <hr ></hr>
                            <div  class="row">
                                <div  class="col-md-10">
                                    <p  class="text-inverse text-left m-b-0">متشکر.</p>
                                    <p  class="text-inverse text-left"><a  href="/#/home"><b >بازگشت به صفحه اصلی</b></a></p>
                                </div>
                                <div  class="col-md-2">
                                    <img  src="files/assets/images//auth/logo-small-bottom.png" alt="small-logo.png"></img>
                                </div>
                            </div>
                        </div>
                    </div>
                  </div>
                
            </div>
            
        </div>
        
    </div>
    
</section>
</template>

<script>
export default {
   
      data:function()
      {
        return {
          username:"",
password:"",
rePassword:"",
ref:"",
isRule:false,

        }
      },
      
    
    methods: { 
      getElementByRef:function(el){
        return this.$refs[el]
      },
      getMyParent:function(obj){return this.$parent},
      setState:function(obj){
        var al={}
        if(obj)
          for(var a in obj)
          {
            //if(al[a])
            //  continue
            try{
              this[a]=obj[a] 
            }catch(exp){}
          }
          
      },
      register:function (){ 
            if(!this.isRule)   
            {
                return notify('ابتدا باید قوانین را قبول کنید','danger')
            }  
            if(this.password!=this.rePassword)
            {
               return notify('رمز عبور با تکرارش برابر نیست','danger') 
            }
            if(!this.username || !this.password)
            {
               return notify('نام کاربری و رمز عبور را وارد کنید','danger') 
            }
            var dt =  httpPostData(global1.apiPath+'/v1/users/registration',{phone:this.username,password:this.password,ref:this.ref})
           
            if(dt.id)
            {
                this.username=""
                this.password=""
                this.ref=""
                this.setState({username:"",password:"",ref:"",rePassword:''})
                return notify('حساب کاربری با موفقیت ایجاد شد','success') 
            } 
            if(!dt.isDone)
            {
                return notify(viewShow(dt),"danger")
            }
            
            
            
        },
        
    },
    
    mounted:function (){
            $('.form-control').on('blur', function() {
                if ($(this).val().length > 0) {
                    $(this).addClass("fill");
                } else {
                    $(this).removeClass("fill");
                }
            });
            $('.form-control').on('focus', function() {
                $(this).addClass("fill");
            });
        },

     
}
</script>