<template>
  <div>
  <div  style="display: none">
      {{ss}}
  </div>
    <div  class="row" v-if="state==states.main">
      <div  class="col-md-4 col-12">
        <sideButton 
          :question="question"
          :tmpQue="tmpQue"
          @addItem="editQuestion($event)" 
          @createWelcome="createWelcome($event,false)" 
          @createEnd="createWelcome($event,true)">
        </sideButton>
      </div>

      <div  class="col-md-8 col-12">
        <questionView
          :question="question"
          @createWelcome="createWelcome($event,false)" 
          @update="update($event)"
          @edit="editQuestion($event)"
          >
        </questionView>
      </div>

    </div>

    <div  class="row" v-if="state==states.welcome || state==states.end">
      <welcomeEditor
        :welcomeText="welcomeText"
        :tmpWel="tmpWel"
        :questionId="questionId"
        @cancel="cancelWelcome()"
        @update="saveWelcome($event)"
      >
      </welcomeEditor>
    </div>

    <div  class="row" v-if="state==states.question">
      <div  class="col-md-4 col-12">
        <questionProp
        :tmpQue="tmpQue"
        :questionId="questionId"
        :question="question"
        @cancelQuestion="cancelWelcome()"
        @saveQuestion="saveQue($event)"
        
        @update="updateData($event)"
        >
        </questionProp>
      </div>
      <div  class="col-md-8 col-12">
        <singleView
        :tmpQue="tmpQue"
        :question="question"
        ref="sview"
        @reloaded="$emit('reloaded',$event)"
        >
        </singleView>
      </div>
    </div>
  <relationModal v-if="showRelationLink" :tempExt="tempExt" 
        :tmpQue="tmpQue" @save="tmpQue=$event">
  </relationModal>
  </div>
</template>

<script>
import relationModal from '../../components/questionnair/relationModal'
import sideButton from './sideButton'
import questionView from './questionView'
import welcomeEditor from './welcomeEditor'
import questionProp from './questionProp'
import singleView from './singleView'
export default {
  name:"questionEditor",
  data:function(){
    return {
      tempExt:[],
      ss:0,
      showRelationLink:false,
      tmpQuestion:[],
      dragableChange:{},
      tmpQue:{},
      tmpWel:{},
      welcomeText:"",
      states:{"main":1,"welcome":2,"end":3,"question":4},
      state:1,
      kinds:{"singleLine":{"min":null,"max":100,"defMax":20,"showMinMax":true,"viewRotate":false,"itmForce":true,"place":true},"decimal":{"min":"","max":"","defMax":"","showMinMax":false,"viewRotate":false,"itmMin":true,"itmMax":true,"itmForce":true,"place":true},"int":{"min":"","max":"","defMax":"","showMinMax":false,"viewRotate":false,"itmMin":true,"itmMax":true,"itmForce":true,"place":true},"multiLine":{"min":null,"max":50,"defMax":500,"showMinMax":true,"viewRotate":false,"itmForce":true,"place":true},"linearScale":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":true,"itmMin":true,"itmMax":true,"itmForce":true,"pNum":true,"pType":"fa-star-o","pDef":6},"numberScale":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":true,"itmMin":true,"itmMax":true,"itmForce":true,"minItm":1,"maxItm":5},"radio":{"min":0,"max":4,"defMax":3,"showMinMax":false,"viewRotate":true,"AutoNext":true},"check":{"min":0,"max":null,"defMax":1,"showMinMax":true,"viewRotate":true},"email":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":false,"itmForce":true,"place":true},"range":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":true,"itmMin":true,"itmMax":true,"itmForce":true},"link":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":false,"itmForce":true,"place":true},"priority":{"min":0,"max":null,"defMax":1,"showMinMax":true,"viewRotate":true},
      
      "page":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":false},
      "phone":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":false},
      
      "file":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":false,"itmMin":true,"itmMax":true,"itmForce":true},"dateTime":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":false,"itmForce":true,"isdate":true}},
      changeMax:false,

    };
  },
  components:{
    relationModal,
    sideButton,
    questionView,
    welcomeEditor,
    questionProp,
    singleView
  },
  methods: {   
    saveQue:function(data){
        console.log(';;;;',data)
        console.log(';;;;1',this.tmpQue)
        console.log(';;;;2',this.question)
        var ex=false
        for(var x of this.question.questions)
        {
          if(x.id==data.id)
            ex=true
        }
        if(!ex)
        this.question.questions.push(data)
      this.state=this.states.main
    },
    updateData:function(q){
      this.tmpQue=q
      var child = this.$refs.sview
      child.reloadItems()
    },
    editQuestion:function (q){
      this.showRelationLink=true;
      this.tmpQue=q
      this.state=this.states.question
    },
    movable:function (){
        

    },
    cancelWelcome:function()
    {
      console.log('canceling')
      this.state=this.states.main
    } ,
    saveWelcome:function(tmwc)
    {
      if(this.state==this.states.end)
        this.question.endPage=tmwc
      else
        this.question.welcomePage=tmwc
      this.tmpWel={}
      this.state=this.states.main

    },
    update:function(tmqu)
    {
      this.question =tmqu
      this.ss++
    },

    createWelcome:function(objx,isEnd)
    {
      console.log('------------>',objx);
      this.tmpWel=objx;
      if(isEnd)
      {
        this.welcomeText="صفحه تشکر"
        this.state=this.states.end
      }
      else
      {
        
        this.welcomeText="صفحه خوش آمد گویی"
        this.state=this.states.welcome
      }
      this.movable();
    },
    addItem:function (objx){ 
      this.changeMax=false 
      this.editQue(objx,true)
    },
    change:function (){
    //dragableChange:{},
    //
      var d=document.getElementsByClassName('sortable-moves')
      var obj=[]
      var tmp={}
      for(var x of d)
      {
        var id=  x.getAttribute('tagid')
        if(id==null)
          return
        for(var j of this.question.questions)
        {
          if(j.id==id)
          {
            if(tmp[j.id])
              break
            tmp[j.id]=true
            obj.push(j)
            break
          }
        }
        //console.log()
      }
      this.question.questions=obj
    },
    reloadThisQuestion:function (){
      for(var a=0;a<this.tmpQuestion.length;a++)
      {
        var xd=this.tmpQuestion[a]
        if(xd.id==this.questionId)
        {
          this.tmpQuestion[a] = this.question
        }
      }
      this.$emit('reload',this.tmpQuestion)
    },
    isDragChange:function (){
      
      var d=document.getElementsByClassName('sortable-moves')
      var obj=[]
      var tmp={}
      for(var x of d)
      {
        var id=  x.getAttribute('tagid')
        for(var j of this.question.questions)
        {
          if(j.id==id)
          {
            if(tmp[j.id])
              break
            tmp[j.id]=true
            obj.push(j.id)
            break
          }
        } 
        //console.log()
      }
        if(JSON.stringify(obj)!=JSON.stringify(this.dragableChange))
        { 
          console.log('changed',  )
          this.ss++
          this.dragableChange=getCopy(obj)
          if(obj.length)
          {
            this.change()
            this.reloadThisQuestion()
          }
        }

    },
  },
  beforeMount:function() {
    
      var t= getCookie('token') 
      var dts=httpGetData(global1.apiPath+'/v1/q/all',{},{'x-auth-token':t}).value


      this.tmpQuestion=[{id:"",name:"خالی"},this.question].concat(getCopy(dts))  
  },
  mounted:function()
  {
    this.movable()
    
      var self=this
      setInterval(function(){
        self.isDragChange()
      },1000)
      this.tempExt=getCopy(this.question.questions)
      console.log('--------------------------------',getCopy(this.question.questions))
      this.ss++;
  },
  props:{
    "question":{default:{}},
    "questionId":{default:""},
  },
}
</script>