<template>
  <div  itemName="item" name="que_number">            <div  v-if="item">              <div  v-if="item.quota && item.quota.capacityRemained&& item.quota.capacity">                 {{item.quota.capacity - item.quota.capacityRemained }}              </div>            </div>          </div>
</template>
<script>
export default {
  name:"que_number",
  props:{"itemName":{"default":"item"},"name":{"default":"que_number"},"bindData":{}}
  ,data:function(){return {"item":{},"que_number":{}} }
  ,mounted:function(){this[this.itemName]=this.bindData }
    
}
</script>