<template>
  
  <div  class="row">
    <div  class="col-md-8">
        <div  class="checkbox-fade fade-in-primary">
            <label >  
                <span  class="text-inverse">{{title}}</span>
            </label>
        </div>
    </div>

    <div  class="col-md-1"> 
          <input  class="js-single" v-on:input="change($event.target.value)"  v-model="doubleValue" type="checkbox"/> 
      </div>
  </div> 
</template>

<script>
export default {
  name:"checkBox",
  props:{
    "title":{default:{}},
    "mvalue":{default:false}
  },
  methods: {
    change:function(value)
    {
      console.log('changes')
      this.$emit('change',value)
    }
  },
  data:function(){
    return { value: false }
  },
  computed: {
    doubleValue: {
        get(){
            //this function will determine what is displayed in the input
            console.log('get' )
            this.value=this.mvalue
            return this.value ;
        },
        set(newVal){
            //this function will run whenever the input changes
            console.log('set')
            this.$emit('change',newVal)
            this.value = newVal ;
        }
    }
}
}
</script>