<template>
  <div>
    
    <div style="display:none">
    {{counter}}
    </div>
      <div  class="row">
          <div  class="col-sm-12">
              <span  class="btn waves-effect waves-light btn-primary btn-outline-primary mfullwidth">
                  نوع سوال : {{getQueType(tmpQue.kind)}}</span>
          </div>  
      </div>  


      
                    <div  class="row" style="overflow-x: hidden;height: 500px;">

                        <div  class="col-md-12 p-20 z-depth-0" style="cursor: pointer;padding: 10px">
                              
                            <div  class="form-group row">
                                <label  class="col-sm-3 col-form-label">سوال</label>
                                <div  class="col-sm-12">
                                  <textEditor 
                                  @input="textChanged($event)"
                                  v-model:tmpQue="tmpQue.text">
                                  </textEditor>
                                     
                                </div>
                            </div> 
                        </div>

                        
                        <div  class="col-md-12 p-20 z-depth-0" style="cursor: pointer;padding: 10px">
                            <div  class="row">
                                <div  class="col-md-8">
                                    <div  class="checkbox-fade fade-in-primary">
                                        <label > 
                                            <span  class="text-inverse">عکس یا ویدیو</span>
                                        </label>
                                    </div>
                                </div>

                                <div  class="col-md-1"> 
                                    <input  class="js-single" v-model="tmpQue.viewUrl" type="checkbox"/> 
                                </div>
                            </div> 
                            
                            <uploader 
                              
                              v-if="tmpQue.viewUrl"
                              @uploaded="tmpQue.url=$event"
                              :item="tmpQue"
                              :questionId="questionId"
                              >
                            </uploader> 
                        </div> 

                        
                        <div  v-if="tmpQue.kind=='numberScale'" class="col-md-12 p-20 z-depth-0" style="cursor: pointer;padding: 10px">
                                 
                            <div  class="row">
                                    <label  class="col-sm-3 col-form-label">تیتر</label>

                            </div> 

                            <div  class="form-group row" v-for="(nsc,index1) in getNumberScale() " :key="nsc.id" >
                                <label  class="col-sm-3 col-form-label">{{'تیتر شماره'+ nsc}}</label>
                                <div  class="col-sm-8">
                                    <input  v-model="tmpQue.header['a'+index1]" type="text" v-on:keyup="numch()" class="form-control form-control-rtl"/>
                                    
                                </div>
                            </div> 
                        </div>


                        <div  v-if="tmpQue.kind=='numberScale'" class="col-md-12 p-20 z-depth-0" style="cursor: pointer;padding: 10px">
                                
                            <div  class="form-group row">
                                    <label  class="col-sm-12 col-form-label">بازه طیف</label>
                                </div> 
                                <div  v-if="kinds[tmpQue.kind].min!=null" class="form-group row">
                                    <label  class="col-sm-3 col-form-label">کمترین</label>
                                    <div  class="col-sm-8">
                                        <input  v-model.number="tmpQue.minItem" type="number" valtype="int" class="form-control form-control-rtl" placeholder="حداقل"/>
                                         
                                    </div>
                                </div> 
                                <div  class="form-group row">
                                    <label  class="col-sm-3 col-form-label">بیشترین</label>
                                    <div  class="col-sm-8">
                                        <input  v-model.number="tmpQue.maxItem" type="number" valtype="int" v-on:keydown="chnageMax()" class="form-control form-control-rtl" placeholder="حداکثر"/>
                                        
                                    </div>
                                </div>
                        </div>
                        
                        <div  class="col-md-12 p-20 z-depth-0" style="cursor: pointer;padding: 10px">
                               <!-- options --> 
                            <options
                                :tmpQue="tmpQue"
                                :question="question"
                                @update="updateData($event) "
                            >
                            </options>
                        </div> 

                        
                        <div  v-if="kinds[tmpQue.kind].showMinMax" class="col-md-12 p-20 z-depth-0" style="cursor: pointer;padding: 10px">
                              
                            <div  class="form-group row">
                                <label  class="col-sm-12 col-form-label">حداقل و حداکثر   :</label>
                            </div> 
                            <div  v-if="kinds[tmpQue.kind].min!=null" class="form-group row">
                                <label  class="col-sm-3 col-form-label">حداقل</label>
                                <div  class="col-sm-8">
                                    <input  v-model="tmpQue.min" type="number" class="form-control form-control-rtl" placeholder="حداقل"/>
                                     
                                </div>
                            </div> 
                            <div  class="form-group row">
                                <label  class="col-sm-3 col-form-label">حداکثر</label>
                                <div  class="col-sm-8">
                                    <input  v-model="tmpQue.max" type="number" v-on:keydown="chnageMax()" class="form-control form-control-rtl" placeholder="حداکثر"/>
                                    
                                </div>
                            </div> 
                        </div>
                        
                        <div  v-if="viewDigit()" class="col-md-12 p-20 z-depth-0" style="cursor: pointer;padding: 10px">
                              
                            <div  class="row">
                              <div  class="col-md-8">
                                  <div  class="checkbox-fade fade-in-primary">
                                      <label >  
                                          <span  class="text-inverse">عدد اعشاری مجاز است</span>
                                      </label>
                                  </div>
                              </div>

                              <div  class="col-md-1"> 
                                    <input  class="js-single" v-model="tmpQue.isDigit" type="checkbox"/> 
                                </div>
                            </div>  
                        </div> 
                        
                        <div  v-if="kinds[tmpQue.kind].pNum" class="col-md-12 p-20 z-depth-0" style="cursor: pointer;padding: 10px">
                              
                                <div  class="row">
                                        <div  class="col-sm-4">
                                            <span >
                                                تعداد نشانگر
                                            </span>
                                        </div> 
                                        <div  class="col-sm-6">
                                                <input  v-model.number="tmpQue.pointerNumber" type="number" valtype="int" class="form-control form-control-rtl" placeholder=""/>
                                        </div>
                                </div>


                                <div  class="row">
                                        <div  class="col-sm-4" style="margin-top: 10px;">
                                            <span >
                                                نوع نشانگر 
                                            </span>
                                        </div>
                                        <div  class="col-sm-5">
                                                <select  v-model="tmpQue.pointerType" class="form-control">
                                                    <option  value="fa-star-o"> 
                                                        <span >ستاره
                                                        </span> 
                                                    </option>
                                                    <option  value="fa-thumbs-o-up"> 
                                                            <span >لایک
                                                            </span> 
                                                    </option>
                                                    <option  value="fa-heart-o"> 
                                                            <span >
                                                                قلب
                                                            </span> 
                                                    </option>
                                                </select> 
                                        </div>
                                        <div  class="col-sm-1" style="margin-top: 10px;">
                                            <span  style="color: red;font-size: 20px;"> 
                                                <i  :class="'fa '+tmpQue.pointerType "></i>
                                            </span>
                                        </div>
                                </div>
                        </div> 
                        
                        <div  class="col-md-12 p-20 z-depth-0" style="cursor: pointer;padding: 10px">
                              <other :tmpQue="tmpQue"
                              @chnageMax="chnageMax()"
                              >
                              </other>


                        </div> 
                        

                        
                        <div  v-if="kinds[tmpQue.kind].viewRotate" class="col-md-12 p-20 z-depth-0" style="cursor: pointer;padding: 10px">
                            <checkBox 
                            :title="'گزینه ها چرخشی نمایش داده شود'"
                            @change="tmpQue.rotation=$event;updating()"
                            :mvalue="tmpQue.rotation"
                            > </checkBox> 
                        </div> 
                        <div  class="col-md-12 p-20 z-depth-0" style="cursor: pointer;padding: 10px">
                            <checkBox 
                            :title="'بازگشت پذیر باشد'"
                            @change="tmpQue.canBack=$event;updating()"
                            :mvalue="tmpQue.canBack"
                            > </checkBox>
                        </div> 
                        <div  v-if="kinds[tmpQue.kind].place" class="col-md-12 p-20 z-depth-0" style="cursor: pointer;padding: 10px">
                              
                            <checkBox 
                            :title="'متن داخل کادر باشد'"
                            @change="tmpQue.place=$event;updating()"
                            :mvalue="tmpQue.place"
                            > </checkBox>  
                        </div> 

                        <div   class="col-md-12 p-20 z-depth-0" style="cursor: pointer;padding: 10px">
                              
                            <checkBox 
                            :title="'دکمه خاتمه داشته باشد'"
                            @change="tmpQue.isEnd=$event;updating()"
                            :mvalue="tmpQue.isEnd"
                            > </checkBox>  
                        </div> 
                        
                        <div  v-if="tmpQue.isEnd" class="col-md-12 p-20 z-depth-0" style="cursor: pointer;padding: 10px">
                               
                            <div  class="form-group row">
                                <label  class="col-sm-3 col-form-label">عنوان خاتمه</label>
                                <div  class="col-sm-8">
                                    <input  v-model="tmpQue.isEndText" type="text" 
                                    class="form-control form-control-rtl" placeholder="عنوان"/>
                                     
                                </div>
                            </div> 
                        </div>
                        
                        <div  class="col-md-12 p-20 z-depth-0" style="cursor: pointer;padding: 10px">
                            <checkBox 
                            :title="'عدد کنار کادر'"
                            @change="tmpQue.showNumber=$event;updating()"
                            :mvalue="tmpQue.showNumber"
                            > </checkBox>  
                        </div> 
                        <div  v-if="kinds[tmpQue.kind].AutoNext" class="col-md-12 p-20 z-depth-0" style="cursor: pointer;padding: 10px">
                            <checkBox 
                            :title="'به صورت خودكار به سوال بعدى برود'"
                            @change="tmpQue.autoNext=$event;updating()"
                            :mvalue="tmpQue.autoNext"
                            >  
                            </checkBox>  
                        </div> 

                    </div>  
 
 
                    <div  class="row">
                        <div  class="col-sm-6">
                            <a  v-on:click="saveQue()" class="btn waves-effect waves-light btn-primary btn-outline-primary mfullwidth">
                               تایید</a>
                        </div>  
                        <div  class="col-sm-6">
                            <a  v-on:click="cancelQue()" class="btn waves-effect waves-light btn-warning btn-outline-warning mfullwidth">
                               لغو</a>
                        </div>  
                    </div>  
  </div>
</template>

<script>
import textEditor from './textEditor'
import uploader from './uploader'
import checkBox from './queProp/checkBox'
import other from './queProp/other'
import options from './queProp/options'
export default {
  name:"questionProp",
  props:{
    "question":{default:{}},
    "questionId":{default:""},
    "tmpQue":{default:{}},
  },
  data:function(){
    return {
      counter:0,
      kinds:{"singleLine":{"min":null,"max":100,"defMax":20,"showMinMax":true,"viewRotate":false,"itmForce":true,"place":true},"decimal":{"min":"","max":"","defMax":"","showMinMax":false,"viewRotate":false,"itmMin":true,"itmMax":true,"itmForce":true,"place":true},"int":{"min":"","max":"","defMax":"","showMinMax":false,"viewRotate":false,"itmMin":true,"itmMax":true,"itmForce":true,"place":true},"multiLine":{"min":null,"max":50,"defMax":500,"showMinMax":true,"viewRotate":false,"itmForce":true,"place":true},"linearScale":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":true,"itmMin":true,"itmMax":true,"itmForce":true,"pNum":true,"pType":"fa-star-o","pDef":6},"numberScale":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":true,"itmMin":true,"itmMax":true,"itmForce":true,"minItm":1,"maxItm":5},"radio":{"min":0,"max":4,"defMax":3,"showMinMax":false,"viewRotate":true,"AutoNext":true},"check":{"min":0,"max":null,"defMax":1,"showMinMax":true,"viewRotate":true},"email":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":false,"itmForce":true,"place":true},"range":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":true,"itmMin":true,"itmMax":true,"itmForce":true},"link":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":false,"itmForce":true,"place":true},"priority":{"min":0,"max":null,"defMax":1,"showMinMax":true,"viewRotate":true},
      "page":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":false},
      "phone":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":false},
      "file":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":false,"itmMin":true,"itmMax":true,"itmForce":true},"dateTime":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":false,"itmForce":true,"isdate":true}},

    };
  },
  components:{
    textEditor,
    checkBox,
    other,
    options,
    uploader
  },
  methods: {
    updating:function(){
        this.counter++;
          this.$emit('update',this.tmpQue)
          console.log('--> data updated ' ,this.tmpQue.showNumber)
    },  
      textChanged:function(data){
          console.log('-->',this.tmpQue.text)
          this.$emit('update',this.tmpQue)
      },
    updateData:function(data){
        this.tmpQue=data
        console.log('----->',data)
          this.$emit('update',this.tmpQue)
    //   this.$emit('saveQuestion',tmpQue)
    }  ,
    viewDigit:function (){
      if(this.tmpQue.kind=="decimal")
      {
        return true
      }
      return false
    },
    saveQue()
    {
     // this.$emit('cancelQuestion','')
       this.$emit('saveQuestion',this.tmpQue)
    },
    cancelQue()
    {
      this.$emit('cancelQuestion','')
    },
    getQueType:function (tp){
      if(tp=="radio")
      {
        return "تک پاسخی"
      }
      if(tp=="singleLine")
      {
        return "متن با پاسخ کوتاه"
      }
      if(tp=="multiLine")
      {
        return "متنی با پاسخ بلند"
      }
      if(tp=="check")
      {
        return "چند پاسخی"
      }
      if(tp=="decimal")
      {
        return "عدد"
      }
      if(tp=="range")
      {
        return "درجه بندی"
      }
      if(tp=="email")
      {
        return "ایمیل"
      }
      if(tp=="link")
      {
        return "لینک و وب سایت"
      }
      if(tp=="linearScale")
      {
        return "طیفی"
      }
      if(tp=="priority")
      {
        return "اولویت دهی"
      }
      if(tp=="page")
      {
        return "متن بدون پاسخ"
      }
      if(tp=="file")
      {
        return "آپلود فایل"
      }
      if(tp=="phone")
      {
        return "وضعیت تماس"
      }
      return ""
    },
    getNumberScale:function (){
      var x=[]
      console.log(this.tmpQue)
      for(var a=this.tmpQue.minItem ;a<=this.tmpQue.maxItem;a++)
      {
        if(!this.tmpQue.header['a'+a])
        this.tmpQue.header['a'+a]=""
        x.push(a)
      }
      return x
    },
  },
  mounted:function()
  {
      var elem = Array.prototype.slice.call(document.querySelectorAll('.js-single')); 
      elem.forEach(function(html) {
        console.log('XXX',html)
        html.setAttribute('class','')
        var switchery = new Switchery(html, { color: '#4099ff', jackColor: '#fff' });
      });  
  },
}
</script>