                        <div  class="col-md-12 p-20 z-depth-0" style="cursor: pointer;padding: 10px">
                              
                            <div  class="row">
                              <div  class="col-md-9">
                                  <div  class="checkbox-fade fade-in-primary">
                                      <label >  
                                          <span  class="text-inverse">عنوان</span>
                                      </label>
                                  </div>
                              </div>
                              <div  class="col-md-2"> 
                                    <input  class="js-single" v-model="tmpWel.viewTitle" type="checkbox"/> 
                                </div>

                            </div>
                            <div  class="form-group row">
                                <label  class="col-sm-3 col-form-label">عنوان</label>
                                <div  class="col-sm-9">
                                    <input  v-model="tmpWel.title" type="text" class="form-control form-control-rtl" placeholder="عنوان"/>
                                </div>
                            </div> 
                        </div> 
                        
                        <div  class="col-md-12 p-20 z-depth-0" style="cursor: pointer;padding: 10px">
                              
                            <div  class="row">
                              <div  class="col-md-9">
                                  <div  class="checkbox-fade fade-in-primary">
                                      <label >  
                                          <span  class="text-inverse">توضیحات</span>
