<script>
import uploader from './uploader'
export default {
  name:"welcomeEditor",
  components:{
    uploader
  },
  props:{
    "tmpWel":{default:{}},
    "question":{default:{}},
    "welcomeText":{default:""},
    "questionId":{default:""},
  },
  methods: {
    saveWelcome:function()
    {
      this.$emit('update',this.tmpWel)
    },
    cancelWelcome:function()
    {
      this.$emit('cancel','')
    }
  },
  mounted:function()
  {
      var elem = Array.prototype.slice.call(document.querySelectorAll('.js-single')); 
      elem.forEach(function(html) {
        console.log('XXX',html)
        html.setAttribute('class','')
        var switchery = new Switchery(html, { color: '#4099ff', jackColor: '#fff' });
      });  
  },
}
</script>