<template>
<ul  class="pcoded-item pcoded-left-item" item-border="true" item-border-style="solid" subitem-border="false"> 
  <li  v-for="(itms,index1) in menuItems " :key="itms.id"  class="pcoded-hasmenu" dropdown-icon="style1" subitem-icon="style1" style=" direction: rtl;">
      <a  href="javascript:void(0)" class="waves-effect waves-dark">
            <span  class="pcoded-micon">
                <img  v-if="itms.icon" v-bind:src="'/icon/'+itms.icon+'.png'" style="height: 20px;"></img>
            </span> 
          <span  class="pcoded-mtext">{{itms.title}}</span>
      </a>
      <ul  class="pcoded-submenu">
          <li  v-for="(itm,index) in itms.children " :key="itm.id"  class=""> 
              <a  v-bind:href="itm.href" class="waves-effect waves-dark" style="direction: rtl;">
                  <span  class="pcoded-mtext">{{itm.title}}</span>
              </a>
          </li>

    
      </ul>
  </li>
</ul>
</template>
<script>
export default {
  name:"appmenu",
  
      data:function()
      {
        return {
          username:"",
          password:"",

        }
      },
      
    
    methods: { 
      getElementByRef:function(el){
        return this.$refs[el]
      },
      getMyParent:function(obj){return this.$parent},
      setState:function(obj){
        var al={"menuItems":true}
        if(obj)
          for(var a in obj)
          {
            //if(al[a])
            //  continue
            try{
              this[a]=obj[a] 
            }catch(exp){}
          }
          
      },
      logout:function (){      
         
      },
        
    },
    
    mounted:function (){ 

    },

    props:{
        'menuItems':{default:function(){ return [{"title":"","children":[{"title":"","href":""}]}]}},
        
      }, 

}
</script>