<template>
<nav  aria-label="..."> 
    <ul  class="pagination">
        <li  v-if="page==0 && isFirst" class="page-item disabled">
          <a  class="page-link" tabindex="-1">&lt;&lt;</a>
        </li>
        <li  v-if="page!=0 && isFirst " class="page-item">
          <a  class="page-link" v-on:click="pageChanged(1)" tabindex="-1">&lt;&lt;</a>
        </li>
         
        <li  v-if="page!=0 && isFirst " class="page-item">
          <a  class="page-link" v-on:click="pageChanged(page)" tabindex="-1">&lt;</a>
        </li>
        
        
        <li  v-for="(itm,index) in arr " :key="itm.id"  :class="itm.cl">

          <a  v-if="itm.isPage" class="page-link">
            {{itm.title}}
          </a>
          <a  v-if="!itm.isPage" class="page-link" v-on:click="pageChanged(itm.title)">
            {{itm.title}}
          </a>
        </li>
          

        <li  class="page-item" v-if="isLast && pagesize!=page+1">
            <a  class="page-link" tabindex="-1" v-on:click="pageChanged(page+2)">	&gt;</a>
        </li> 
         
        <li  class="page-item disabled" v-if="isLast && pagesize==page+1">
            <a  class="page-link">&gt;&gt;</a>
        </li> 

        <li  class="page-item" v-if="isLast && pagesize!=page+1">
            <a  class="page-link" tabindex="-1" v-on:click="pageChanged(pagesize)">&gt;&gt;</a>
        </li> 
    </ul>
    <div  style="display: none">
      
    {{itemCount}}
    </div> 
  </nav>


</template> 
<script>
export default {
  name:"appPagination",    
      data:function()
      {
        return {
          arr:[],
          pagesize:0,

        }
      },
      
    
    methods: { 
      getElementByRef:function(el){
        return this.$refs[el]
      },
      getMyParent:function(obj){return this.$parent},
      setState:function(obj){
        var al={"isLast":true,"isFirst":true,"begin":true,"middle":true,"end":true,"page":true,"itemCount":true,"itemPerPage":true}
        if(obj)
          for(var a in obj)
          {
            //if(al[a])
            //  continue
            try{
              this[a]=obj[a] 
            }catch(exp){}
          }
          
      },
      pageChane:function(param)
        {
          this.$emit('pageChane', param)
        } ,
        pageChanged:function (itm){ 
          this.page=itm-1
          this.update()
          if(this.pageChane)
            this.pageChane(itm)
          
        },
        update:function (){ 

           
           var pagesize=Math.floor(this.itemCount/this.itemPerPage)
           if(this.itemCount%this.itemPerPage!=0)
               pagesize++
           var mp=[]
           var m=Math.floor(this.middle/2)
           for(var a=1;a<=pagesize;a++)
           {
               if(a<=this.begin)
               {
                   mp.push(a)
                   continue
               }
               if(a>=this.page-m && a<=this.page+m)
               {
                   if( a-1!=mp[mp.length-1])
                   {
                       mp.push('...')
                   }
                   mp.push(a)
                   continue
               } 
               
               if(a>  pagesize-this.end)
               {
                   if( a-1!=mp[mp.length-1])
                   {
                       mp.push('...')
                   }
                   mp.push(a) 
               } 
           }
           console.log('ccccccccccc ',mp)
           var mp1=[]
           for(var a of mp)
           {
               var obj={title:a,cl:"page-item"}
               if(a==this.page+1)
               {
                  obj.cl+=" active"
                  obj.isPage=true
               } 
               mp1.push(obj)
               
           }
           if(JSON.stringify(this.arr)!=JSON.stringify(mp1))
           {

              this.setState({arr:mp1,pagesize:pagesize}) 
           }
           //this.setState({arr:mp1}) 
           console.log('update',this.arr)
        },
        
    },
    
    mounted:function (){ 
        this.update()
      },
    updated:function (){ 
        
        this.update()
      },

    props:{
        'isLast':{default:true},
        'isFirst':{default:true},
        'begin':{default:2},
        'middle':{default:2},
        'end':{default:2},
        'page':{default:0},
        'itemCount':{default:0},
        'itemPerPage':{default:1},
        
      }, 
}
</script>