<template>
<div class="row"> 
  <input  type="file" id="media" style="display: none"/>
  <div  style="display: none">
      {{ss}}
  </div>
  <div  class="col-sm-12">
    <div  class="card"> 
      <div  class="card-header">
          <h5 >پرسشنامه</h5>
          <span ></span>
      </div>
      <div  class="card-block">
        <ul  class="nav nav-tabs md-tabs " role="tablist">
          <li  class="nav-item" v-if="question.name" @click="subPage=1">
            <a  class="nav-link active" data-toggle="tab" href="#home7" role="tab"><i  class="icofont icofont-home"></i>ایجاد</a>
            <div  class="slide"> 
            </div>
          </li>
          <li  class="nav-item" v-if="question.name" @click="subPage=2">
            <a  class="nav-link" data-toggle="tab" href="#condition" role="tab"><i  class="icofont icofont-ui-user "></i>اعمال شرط</a>
            <div  class="slide"> 
            </div>
          </li>
          <li  class="nav-item" v-if="question.name" @click="subPage=3">
            <a  class="nav-link" data-toggle="tab" href="#quota" role="tab"><i  class="icofont icofont-ui-user "></i>اعمال سهمیه</a>
            <div  class="slide"> 
            </div>
          </li>            
          <li  class="nav-item" @click="subPage=4">
            <a  class="nav-link" data-toggle="tab" href="#setting" role="tab"><i  class="icofont icofont-ui-message"></i>تنظیمات</a>
            <div  class="slide"> </div>
          </li>
        </ul>

        <div  class="tab-content card-block">
          <!-- for editor -->
          <div v-if="subPage==1"  class="tab-pane active" id="home7" role="tabpanel">
            <questionEditor v-if="completLoading" v-bind:question="question" :questionId="questionId" 
            @reloaded="questionAnsw=$event" 
            
            >
            </questionEditor>
          </div>
          <!-- for conditions -->
          <div  v-if="subPage==2" class="tab-pane" id="condition" role="tabpanel">
            <conditionView
            :question="question"
            :tmpQuestion="tmpQuestion"
            :questionAnsw="questionAnsw"
            >
            </conditionView>
          </div>
          <!-- for quta -->
          <div  v-if="subPage==3"  class="tab-pane" id="quota" role="tabpanel">
            <localQuta :qutas="question.quota" >
            </localQuta>

          </div>
          <!-- for setting -->
          <div  v-if="subPage==4" class="tab-pane" id="setting" role="tabpanel">
            <setting v-bind:question="question" :questionId="questionId" @update="reloadThisQuestion($event)">
            </setting>
          </div>
        </div>


      </div>
    </div>
  </div>
 
  <!-- Modal for Question 
  
  <div class="modal fade" id="editModal" tabindex="-1" role="dialog">
  </div>--> 




  
      <div  class="modal fade" id="questionModal1" tabindex="-1" role="dialog">
            <div  class="modal-dialog" role="document">
                <div  class="modal-content">
                <div  class="modal-header">
                <h4  class="modal-title"> اطلاعات اولیه</h4>
                <button  type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span  aria-hidden="true">&times;</span>
                </button>
                </div>
                    <div  class="modal-body"> 
                        
                      <div  class="card-block">
        
  
                    
                          
                        <div  class=" card-sub qitem" style="padding: 10px">
                           
                           
                           <div  class="row">
                               <div  class="col-md-3">
                                 <h5  class="card-title"> نام </h5>
                               </div>
                               <div  class="col-md-9">   
                                 <input  type="text" v-model="question.name"/>
                               </div>
                           </div>    
                           <div  class="row">
                                <div  class="col-md-3">
                                  <h5  class="card-title"> کارفرما </h5>
                                </div>
                                <div  class="col-md-9">   
                                        <select  v-model="question.empl" class="form-control">
                                                <option  value="11"> کاله </option>
                                                <option  value="12"> دامدارن </option>
                                                <option  value="13"> پگاه </option>
                                            </select> 
                                </div>
                            </div>  
                           <div  class="row">
                                <div  class="col-md-3">
                                  <h5  class="card-title"> دسته بندی</h5>
                                </div>
                                <div  class="col-md-9">   
                                  <input-tag  type="text" v-model="question.tag" >
                                </input-tag>
                                </div>
                            </div>  
                               
                         </div>
        
                      </div>
                    </div>
                <div  class="modal-footer">
                <button  type="button" class="btn btn-default waves-effect " v-on:click="draft()" data-dismiss="modal">تایید</button> 
                </div>
                </div>
            </div>
         </div>
</div>        
</template>

<script>
import setting from '../../components/questionnair/setting'
import questionEditor from '../../components/questionnair/questionEditor'
import conditionView from '../../components/questionnair/conditionView'
import localQuta from '../../components/questionnair/local/quta'
export default {
  name:"Questionnaire",
  components:{ 
    setting,
    questionEditor,
    conditionView,
    localQuta
  },
  data:function()
  {
    return {
      subPage:1,
      completLoading:false,
      questionAnsw:{},///EXXXXXXXXXXXXXXXXXXXx
      tmpQuestion:[],
      questionId:"",
      tmpQue:{},
      ss:0,
      question:{"_id":"thisQuestion","conditions":[],"isPublish":false,"rotation":false,"point":0,"chance":0,"name":"s","duration":0,"welcomePage":null,"endPage":null,"quota":{"type":"and","children":[]},"questions":[]},

    }
  },
  methods:{ 
    draft:function (){
      
      var t= getCookie('token') 
      var dts=httpPostData(global1.apiPath+'/v1/q',this.question,{'x-auth-token':t})
      if(dts.id)
      {
        window.location.href="/#/home/questionnaire/"+dts.id   
        setInterval(function(){

          location.reload();
        },1000)     
        this.questionId=dts.id
        this.question.id=dts.id
      }
    },
    reloadThisQuestion:function(tmquest)
    {
      console.log('sss-->',tmquest)
    },
    convertToText:function(data){
      return $(data).text()
    },
    selectToName:function (name){
      if(name=="all")
      {
        return "همه"
      }
      if(name=="selected")
      {
        return "انتخاب شده " 
      }
      if(name=="notSelected")
      {
        return "انتخاب نشده" 
      }
      return ""
    },
    openExternal:function(){
      
      $("#editModal").modal()
    },
    reloadQuestion:function (question){
      question=getCopy(question)
      var questionAnsw={}
      var isexist=false
      for(var a of question.questions)
      {
        if(a.id==this.tmpQue.id)
        {
          isexist=true
        }
      }
      if(!isexist)
      {
        question.questions.push(this.tmpQue)
      }
      for(var a of question.questions)
      {
        var n=""+a.id
        questionAnsw[n]={}
        for(var b of a.answers)
        {
          questionAnsw[n][a.id+'_'+b.id] ={id:a.id+'_'+b.id,q:a.id,a:b.id,text:b.text,expected:b.expected,url:b.url}
        }
        if(a.other=="true")
        {
          for(var j=0;j<a.otherNum;j++)
            questionAnsw[n]['o'+a.id+'_'+j] ={id:'o'+a.id+'_'+j,a:j,text:a.otherText,isOther:true}
        }
        if(a.external)
        { 
            for(var j of a.external)
            {
              var i = questionAnsw[j.id+""] 
              for(var xx in i)
              {
                var x=i[xx]
                var canp=false  
                  questionAnsw[n][ x.id]={id: x.id,q:x.q,a:x.id,text:x.text}
              } 
            } 
        }         
      } 
      return questionAnsw
    },
  },
  mounted:function()
  {
    var id=getParams(this,"id") 
    console.log('id is :---->',id)
    if(id=='none')
    {
      setPageName('پرسشنامه جدید')
      $("#questionModal1").modal() 
    }
    else
    {
        console.log('token is is :')
        var t= getCookie('token')
        console.log('token is is :',t)
        this.questionId=id
        var dts=httpGetData(global1.apiPath+'/v1/q/'+id,{},{'x-auth-token':t})
        if(!dts.conditions)
          dts.conditions=[]
         setPageName('پرسشنامه  ' + dts.name)
        console.log('---->',dts) 
        this.question=dts
      this.tempExt=getCopy(this.question.questions)
        // this.setState({question:dts})
        
        
        var dts=httpGetData(global1.apiPath+'/v1/q/all',{},{'x-auth-token':t}).value 
        this.tmpQuestion=[{id:"",name:"خالی"},this.question].concat(getCopy(dts))   


        //this.reloadQuestion()

        // this.ss++
        this.completLoading=true;
    }
    global1.questionPage={
      //this for Modal of relation
      tempExt:[],
      tmpQue:{}
    };
  }
}
</script>