<template>
  <div>
                    <div  style="top: 10%;position: inherit;">
                        <div  class="row">
                            <div  class="col-md-12" v-html="tmpQue.text" style="height: 100px;"> 
                                
                            </div>
                        </div> 
                        <div  v-if="tmpQue.viewUrl" class="row" style="top: 50%;position: inherit;font-weight: bolder;font-size: x-large;">
                            <div  class="col-md-12"> 
                                <img  v-bind:src="tmpQue.url" v-if="tmpQue.isPicture" style="max-width: 300;"></img> 
                                
                                <video  v-if="tmpQue.url && !tmpQue.isPicture" width="320" height="240" controls>
                                <source :src="tmpQue.url" type="video/mp4">
                                Your browser does not support the video tag.

                                </video>
                            </div>
                         </div> 
                        <div  v-if="tmpQue.kind=='numberScale'">
                           <table  border="1">
                               <tr >
                                    <th  v-if="tmpQue.showNumber">
                                        شماره
                                    </th>
                                    <th >
                                        عنوان
                                    </th>
                                    <td  v-for="(nsc,index1) in getNumberScale() " :key="nsc.id" >
                                        <div  v-if="tmpQue.header['a'+index1]"> 
                                            {{tmpQue.header['a'+index1] }}
                                        </div>
                                    </td>
                               </tr>

                               <tr  v-for="(ans,index) in tmpQue.answers " :key="ans.id" >
                                    <td  v-if="tmpQue.showNumber" style="padding: 15px;">
                                        {{index+1}}
                                    </td>
                                    <td  style="padding: 15px;">
                                            {{ans.text}}
                                    </td>
                                    <td  v-if="ans.url" style="padding: 15px;">
                                           
                                        <img  v-bind:src="ans.url" class="fix-image"></img>
                                    </td>
                                    <td  v-for="(nsc,index1) in getNumberScale() " :key="nsc.id"  style="padding: 15px;">
                                        {{nsc}}
                                    </td>
                               </tr>
                               
                           </table>
                        </div>    
                        
                      <div  v-for="(ans,index) in newItem " :key="ans.id"  v-if="tmpQue.kind!='numberScale'" class="form-group row" style="text-align: right;">
                            <label  class="col-sm-1 col-form-label" v-if="!ans.isOther">
                                <span  v-if="tmpQue.showNumber">

                                    {{index+1}}
                                </span>
                                
                            </label>
                            
                          <div  class="col-sm-7 col-form-label" style="padding-top: 0px;" v-if="!ans.isOther">
                             
                            <div  v-if="tmpQue.kind=='radio'" class="radio radiofill radio-primary radio-inline">
                                <label >
                                    <input  type="radio" name="radio" style="display: none"/>
                                    <img  v-if="ans.url" v-bind:src="ans.url" class="fix-image"></img>
                                    <i  class="helper"></i>{{ans.text}}
                                </label>  

                            </div>

                            <div  v-if="tmpQue.kind=='singleLine'|| tmpQue.kind=='link'|| tmpQue.kind=='email' " class="radio radiofill radio-primary radio-inline">

                                <label  v-if="tmpQue.place">
                                    <input  type="text" v-bind:placeholder="ans.text"/> 
                                </label>  
                                <img  v-if="ans.url" v-bind:src="ans.url" class="fix-image"></img>
                                <label  v-if="!tmpQue.place">
                                     {{ans.text}}
                                    <input  type="text"/>
                                </label>  
                            </div>

                            
                            <div  v-if="tmpQue.kind=='priority'" class="col-form-label row">
                                <div  class="col-sm-1 col-form-label">

                                 <div  style="border: 2px solid #69afff;color: #69afff;width: 30px;height: 30px;">

                                 </div>
                                </div>
                                 <div  class="col-sm-4 col-form-label">
 
                                        <img  v-if="ans.url" v-bind:src="ans.url" class="fix-image"></img>
                                     <span >
                                         {{ans.text}}
                                     </span>
                                 </div>
                            </div>
                            

                            <div  v-if="tmpQue.kind=='range'" class="col-form-label row">
                                 <div  class="col-sm-4 col-form-label">
 
                                        <img  v-if="ans.url" v-bind:src="ans.url" class="fix-image"></img>
                                     <span >
                                         {{ans.text}}
                                     </span>
                                 </div>
                                <div  class="col-sm-1 col-form-label">
                                        <div  class="range-slider">
                                                <div > 
                                                    <input  class="ex18b" type="text"/>
                                                </div>
                                            </div>
                                </div> 
                            </div>

                            <div  v-if="tmpQue.kind=='dateTime'" class="col-form-label row">
                                 <div  class="col-sm-4 col-form-label">
 
                                        <img  v-if="ans.url" v-bind:src="ans.url" class="fix-image"></img>
                                     <span >
                                         {{ans.text}}
                                     </span>
                                 </div>
                                <div  class="col-sm-6 col-form-label">
                                        <div >
                                                <div > 
                                                    
                                                    <date-picker  v-bind:clearable="true"></date-picker>
                                                </div>
                                            </div>
                                </div> 
                            </div>

                            <div  v-if="tmpQue.kind=='file'" class="col-form-label row">
                                 <div  class="col-sm-4 col-form-label">
 
                                        <img  v-if="ans.url" v-bind:src="ans.url" class="fix-image"></img>
                                     <span >
                                         {{ans.text}}
                                     </span>
                                 </div>
                                <div  class="col-sm-6 col-form-label">
                                        <div >
                                                <div > 
                                                    
                                                    <input  type="file"/>
                                                </div>
                                            </div>
                                </div> 
                            </div>
                            

                            <div  v-if="tmpQue.kind=='linearScale' " class="col-form-label row">
                                <div  class="col-sm-4 col-form-label">

                                        <img  v-if="ans.url" v-bind:src="ans.url" class="fix-image"></img>
                                    <span >
                                        {{ans.text}}
                                    </span>
                                </div>
                                <div  class="col-sm-4 col-form-label" style="top:-20px;height: 40px">
                                    <tool-rating  v-bind:number="tmpQue.pointerNumber" v-bind:shape="tmpQue.pointerType"></tool-rating>
                                     
                                </div>  
                            </div>

                            <div  v-if="tmpQue.kind=='decimal' || tmpQue.kind=='int'" class="radio radiofill radio-primary radio-inline">
                                
                              <img  v-if="ans.url" v-bind:src="ans.url" class="fix-image"></img>
                              <label  v-if="tmpQue.place">
                                    <input  type="number" v-bind:placeholder="ans.text"/> 
                                </label>  
                                <label  v-if="!tmpQue.place">
                                     {{ans.text}}
                                     
                                    <input  type="number"/>
                                </label>  
                            </div>

                            <div  v-if="tmpQue.kind=='multiLine' " class="radio radiofill radio-primary radio-inline">
                                 
                                
                                <label  v-if="tmpQue.place">
                                        <textarea  type="text" v-bind:placeholder="ans.text">
    
                                        </textarea>
                                </label>  
                                <label  v-if="!tmpQue.place">
                                    <table >
                                        <tr >
                                            <td >
                                                    <img  v-if="ans.url" v-bind:src="ans.url" class="fix-image"></img>
                                                <span  style="">
                                                    
                                                {{ans.text}}
                                                </span>

                                            </td>
                                            <td >
                                                <textarea  type="text">
            
                                                </textarea>

                                            </td>
                                        </tr>
                                    </table>
                                </label>   
                            </div> 
                            <div  v-if="tmpQue.kind=='check'" class="checkbox-fade fade-in-primary"> 

                                <label > 
                                    <input  type="checkbox" value=""/>
                                    <span  class="cr"><i  class="cr-icon icofont icofont-ui-check txt-primary"></i></span>
                                </label> 
                                
                                <label  style="top: -10px;position: relative;"> 
                                        <img  v-if="ans.url" v-bind:src="ans.url" class="fix-image"></img>
                                    <span  class="text-inverse">{{ans.text}}</span> 
                                </label>     
                            </div>
                          </div>
                          
                          
                      </div> 
                  
                        
                        

                        <div  v-for="(cnt,indxx) in getCounter(tmpQue.otherNum) " :key="cnt.id"  class="form-group row" style="text-align: right;"> 
                        
                            <label  class="col-sm-1 col-form-label">
                                    
                                    <span  v-if="tmpQue.showNumber">
                                            *
                                    </span> 
                            </label>
                            <div  class="col-sm-9 col-form-label" style="padding-top: 0px;">
                                     
                                <div >
                                    
                                    <label  v-if="tmpQue.place">
                                        <input  type="text" v-bind:placeholder="tmpQue.otherText"/> 
                                    </label>  
                                    <label  v-if="!tmpQue.place">
                                            {{tmpQue.otherText}}
                                        <input  type="text"/>
                                    </label>  
 
                                </div> 
                            </div> 
                        </div> 

                        
                    </div>
    
  </div>
</template>

<script>
export default {
  name:"singleView",
  data:function()
  {
      return {
          newItem:{},
          ssquestion:{}
      };
  },
  props:{
    "tmpQue":{default:{}},
    "question":{default:{}},
  },
  methods: { 
    reloadQuestion:function (question){
      question=getCopy(question)
      var questionAnsw={}
      //this.tmpQue
      var isexist=false
      for(var a of question.questions)
      {
        if(a.id==this.tmpQue.id)
        {
          isexist=true
        }
      }
      if(!isexist)
      {
        question.questions.push(this.tmpQue)
      }
      for(var a of question.questions)
      {
        var n=""+a.id
        questionAnsw[n]={}
        for(var b of a.answers)
        {
          questionAnsw[n][a.id+'_'+b.id] ={id:a.id+'_'+b.id,q:a.id,a:b.id,text:b.text,expected:b.expected,url:b.url}
        }
        if(a.other=="true")
        {
          for(var j=0;j<a.otherNum;j++)
            questionAnsw[n]['o'+a.id+'_'+j] ={id:'o'+a.id+'_'+j,a:j,text:a.otherText,isOther:true}
        }
        if(a.external)
        { 
            for(var j of a.external)
            {
              var i = questionAnsw[j.id+""] 
              for(var xx in i)
              {
                var x=i[xx]
                var canp=false  
                  questionAnsw[n][ x.id]={id: x.id,q:x.q,a:x.id,text:x.text}
              } 
            } 
        }
         
      } 
      console.log('questionAnsw-------> ',questionAnsw)
      this.$emit('reloaded',questionAnsw)
      return questionAnsw
    },  
    reloadItems:function (){
      if(! this.tmpQue || !this.tmpQue.id)
      {
        if(this.newItem.length>0)
          this.newItem=[]
        return
      }
      if(JSON.stringify(this.tmpQue)!=JSON.stringify(this.ssquestion))
      {
        this.ssquestion=getCopy(this.tmpQue) 
        var item= this.reloadQuestion(this.question)
      console.log('questionAnsw-------> ',item)
        this.newItem=[]

        for(var x in item[this.tmpQue.id+''])
        { 
          this.newItem.push(item[this.tmpQue.id+''][x])
        } 
      }

    },
    getCounter:function (a,can){
      var x=[]
      if(!this.tmpQue.other)
        return x
      for(var i=0;i<a;i++ )
        x.push(true)
      return x  
    },
    getNumberScale:function (){
      var x=[] 
      for(var a=this.tmpQue.minItem ;a<=this.tmpQue.maxItem;a++)
      {
        if(!this.tmpQue.header['a'+a])
        this.tmpQue.header['a'+a]=""
        x.push(a)
      }
      return x
    },
  },
  mounted:function() {
      this.reloadItems();
  },
}
</script>