import Vue from 'vue'
import Router from 'vue-router' 
import Login from './views/pages/auth/login'
import Forget from './views/pages/auth/forget'
import Register from './views/pages/auth/register'
import Home from './views/pages/home/home'
import Users from './views/pages/home/users'
import Questionnaire from './views/pages/questionnaire/questionnaire'
// import ActiveQuestionnaire from './views/pages/questionnaire/activeQuestionnaire'
import DraftQuestionnaire from './views/pages/questionnaire/draftQuestionnaire'

import InputTag from 'vue-input-tag'
Vue.component('input-tag', InputTag)

Vue.use(Router)

export default new Router({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      beforeEnter:function (to, from, next) 
      {
        if(!getCookie('token'))
        {
          redirectTo('/#/login')
          next(false)
        }
        else
        {
          next(true)
        }

      }
    },
    {
      path: '/home',
      name: 'home',
      component: Home,
      children:[
        {path:"users",component:Users, },
        {path:'questionnaire/:id' ,component:Questionnaire},
        // {path:'activequestionnaire' ,component:activeQuestionnaire},
        // {path:'notActiveQuestionnaire' ,url:'./pages/home/questionnaire/notActiveQuestionnaire/notActiveQuestionnaire.js'    },
        {path:'draftQuestionnaire' ,component:DraftQuestionnaire   },
        // {path:'deletedtQuestionnaire' ,url:'./pages/home/questionnaire/deleted/deleted.js'    },
        // {path:'notVerify' ,url:'./pages/home/questionnaire/notVerify/notVerify.js'    },
      ],
      beforeEnter:function (to, from, next) 
      {
        if(!getCookie('token'))
        {
          redirectTo('/#/login')
          next(false)
        }
        else
        {
          next(true)
        }

      }
    },
    ///Login page
    {
      path: '/login',
      name: 'login',
      component: Login,
      beforeEnter:function(to, from, next){
        if(getCookie('token'))
        {
          redirectTo('/home')
          next(false)
        }
        else
        {
          next(true)
        }
      }
    },
    //forget
    {
      path: '/forget',
      name: 'forget',
      component: Forget,
      beforeEnter:function(to, from, next){
        if(getCookie('token'))
        {
          redirectTo('/home')
          next(false)
        }
        else
        {
          next(true)
        }
      }
    },
    //forget
    {
      path: '/register',
      name: 'register',
      component: Register,
      beforeEnter:function(to, from, next){
        if(getCookie('token'))
        {
          redirectTo('/home')
          next(false)
        }
        else
        {
          next(true)
        }
      }
    },
    
    
  ]
})
