<template>
  <div class="row">
    <div style="display:none">
    {{counter}}
    </div>
     <div class="col-6">
        <div>
          نام سهمیه
        </div>
        <div>
          <input v-model="data.name"/>
        </div>
        <div>
          لیست شماره تلفن
        </div>
        <div>
          <textarea v-model="data.phoneNumber"/>
        </div>
        <div>
          تعداد سهمیه
        </div>
        <div>
          <input v-model="data.number"/>
        </div>
        <div style="height:10px">
        </div>
     </div>
     <div class="col-6">
        
          <div style="height:70px">
          </div> 
          <div v-if="data.active">
              <button  v-on:click="deactiveQuta()" class="btn waves-effect waves-light btn-danger btn-icon">
                  <i  class="fa fa-pause"></i>
              </button>  
          </div>
          <div v-if="!data.active">
              <button  v-on:click="activeQuta()" class="btn waves-effect waves-light btn-success btn-icon">
                  <i  class="fa fa-play"></i>
              </button>  
          </div>
     </div> 
  </div>
</template>

<script>
export default {
  name:"subQuta",
  props:{
    data:{default:{}}
  },
  data() {
    return {
      counter:1
    }
  },
  methods: {
    activeQuta(){
      this.data.active=true;
      this.counter++;
    },
    deactiveQuta()
    {
      this.data.active=false;
      this.counter--
    }
  },
}
</script>