<template>
  <div>
    
                    <div  class="row">
                        <div  class="col-sm-12">
                            <button  v-on:click="createWelcome()" class="btn waves-effect waves-light btn-primary btn-outline-primary mfullwidth">
                               صفحه خوش آمد گویی</button>
                        </div>  
                    </div>  

                    <div  class="row">
                        <div  class="col-sm-6">
                            <button  v-on:click="addItem('singleLine')" class="btn waves-effect waves-light btn-primary btn-outline-primary mfullwidth">
                              متن با پاسخ کوتاه</button>
                        </div>  
                        <div  class="col-sm-6">
                            <button  v-on:click="addItem('radio')" class="btn waves-effect waves-light btn-primary btn-outline-primary mfullwidth">
                              تک پاسخی</button>
                        </div>  
                    </div>  

                    
                    <div  class="row">
                        <div  class="col-sm-6">
                            <button  v-on:click="addItem('multiLine')" class="btn waves-effect waves-light btn-primary btn-outline-primary mfullwidth">
                             متنی با پاسخ بلند </button>
                        </div>  
                        <div  class="col-sm-6">
                            <button  v-on:click="addItem('check')" class="btn waves-effect waves-light btn-primary btn-outline-primary mfullwidth">
                             چند پاسخی </button>
                        </div>  
                    </div>  


                    


                    <div  class="row">
                        <div  class="col-sm-6">
                            <button  v-on:click="addItem('linearScale')" class="btn waves-effect waves-light btn-primary btn-outline-primary mfullwidth">
                            طیفی
                            (نشانگر)
                                 </button>
                        </div>  
                        <div  class="col-sm-6">
                            <button  v-on:click="addItem('numberScale')" class="btn waves-effect waves-light btn-primary btn-outline-primary mfullwidth">
                            طیفی
                            (عددی)
                                 </button>
                        </div>  
                    </div>  


                    <div  class="row">
                        <div  class="col-sm-6">
                            <button  v-on:click="addItem('email')" class="btn waves-effect waves-light btn-primary btn-outline-primary mfullwidth">
                             ایمیل </button>
                        </div>  
                        <div  class="col-sm-6">
                            <button  v-on:click="addItem('page')" class="btn waves-effect waves-light btn-primary btn-outline-primary mfullwidth">
                             متن بدون پاسخ </button>
                        </div>  
                        
                    </div>  


                    <div  class="row">
                        <div  class="col-sm-6">
                            <button  v-on:click="addItem('link')" class="btn waves-effect waves-light btn-primary btn-outline-primary mfullwidth">
                             لینک / وب سایت </button>
                        </div>  
                        <div  class="col-sm-6">
                            <button  v-on:click="addItem('priority')" class="btn waves-effect waves-light btn-primary btn-outline-primary mfullwidth">
                             اولویت دهی </button>
                        </div>  
                    </div>  


                    <div  class="row">
                        <div  class="col-sm-6">
                            <button  v-on:click="addItem('file')" class="btn waves-effect waves-light btn-primary btn-outline-primary mfullwidth">
                             آپلود فایل </button>
                        </div>  
                        <div  class="col-sm-6">
                            <button  v-on:click="addItem('dateTime')" class="btn waves-effect waves-light btn-primary btn-outline-primary mfullwidth">
                             تاریخ </button>
                        </div>  
                    </div>  

                    <div  class="row">
                        
                            <div  class="col-sm-6">
                                    <button  v-on:click="addItem('decimal')" class="btn waves-effect waves-light btn-primary btn-outline-primary mfullwidth">
                                     عدد </button>
                                </div>  
                        <div  class="col-sm-6">
                            <button  v-on:click="addItem('phone')" class="btn waves-effect waves-light btn-primary btn-outline-primary mfullwidth">
                            وضعیت تماس </button>
                        </div>  
                      
                    </div>  


                    <div  class="row">
                        <div  class="col-sm-12">
                            <button  v-on:click="createWelcome(true)" class="btn waves-effect waves-light btn-primary btn-outline-primary mfullwidth">
                               صفحه تشکر</button>
                        </div>  
                    </div>  
                    
  </div>
</template>

<script>
export default {
  name:"sideButton",
  props:{
    "question":{default:{}}, 
    "tmpQue":{default:{}}, 
  },
  data:function(){
    return {
       kinds:{"singleLine":{"min":null,"max":100,"defMax":20,"showMinMax":true,"viewRotate":false,"itmForce":true,"place":true},
       "decimal":{"min":"","max":"","defMax":"","showMinMax":false,"viewRotate":false,"itmMin":true,"itmMax":true,"itmForce":true,"place":true},
       "phone":{"min":"","max":"","defMax":"","showMinMax":false,"viewRotate":false,"itmMin":false,"itmMax":false,"itmForce":false,"place":false},
       
       "int":{"min":"","max":"","defMax":"","showMinMax":false,"viewRotate":false,"itmMin":true,"itmMax":true,"itmForce":true,"place":true},"multiLine":{"min":null,"max":50,"defMax":500,"showMinMax":true,"viewRotate":false,"itmForce":true,"place":true},"linearScale":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":true,"itmMin":true,"itmMax":true,"itmForce":true,"pNum":true,"pType":"fa-star-o","pDef":6},"numberScale":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":true,"itmMin":true,"itmMax":true,"itmForce":true,"minItm":1,"maxItm":5},"radio":{"min":0,"max":4,"defMax":3,"showMinMax":false,"viewRotate":true,"AutoNext":true},"check":{"min":0,"max":null,"defMax":1,"showMinMax":true,"viewRotate":true},"email":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":false,"itmForce":true,"place":true},"range":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":true,"itmMin":true,"itmMax":true,"itmForce":true},"link":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":false,"itmForce":true,"place":true},"priority":{"min":0,"max":null,"defMax":1,"showMinMax":true,"viewRotate":true},
       "page":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":false},"file":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":false,"itmMin":true,"itmMax":true,"itmForce":true},"dateTime":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":false,"itmForce":true,"isdate":true}},

    }
  },
  methods: {
    createWelcome:function (isEnd){
      var tmpWel={
        viewUrl:false,
        isPicture:true,
        url:"",
        viewTitle:true,
        title:"",
        viewDescription:true,
        description:"",
        viewButtonTitle:true,
        buttonTitle:"",
      }
      if(isEnd){
        tmpWel.title="ارسال کنید"
        tmpWel.buttonTitle="ارسال" 
        if(this.question.endPage)
          tmpWel=getCopy(this.question.endPage)
          
        this.$emit('createEnd',tmpWel)
      }  
      else  
      { 
        tmpWel.buttonTitle="شروع" 
        if(this.question.welcomePage)
          tmpWel=getCopy(this.question.welcomePage)  
        this.$emit('createWelcome',tmpWel)

      }
      
    },
    getNewAnswId:function (){
      var max=0
      if(Array.isArray(this.tmpQue.answers))
        for(var a of this.tmpQue.answers)
        {
          if(max<a.id)
          {
            max=a.id
          }
        }
      return max+1
    },
    getNewNumber:function (){
      var max=0
      for(var a of this.question.questions)
      {
        if(max<a.id)
        {
          max=a.id
        }
      }
      return max+1
    },
    getNewId:function (){
      var max=0
      for(var a of this.question.questions)
      {
        if(max<a.number)
        {
          max=a.number
        }
      }
      return max+1
    },
    addItem:function(type)
    {
      console.log(type)
      var answs={id:this.getNewAnswId(),text:""}
      if(this.kinds[type].pNum )
      {
        answs.type= this.kinds[type].pType
        answs.pnum= this.kinds[type].pDef
      }
      if(type=="dateTime")
      {
        answs.year=true
        answs.month=true
        answs.day=true
        answs.setting=true
      }
      var objx={
        id:this.getNewNumber(),
        number:this.getNewId(),
        text:'',
        kind:[type],
        min:0,
        max:2500,
        canBack:true,
        isDigit:false,
        answers:[answs],
        required:true,
        external:[],
        otherNum:1,
        autoNext:true,
        otherText:'سایر موارد',
        conditionRequired:false,
        condition:{
          type:'and', 
          children:[]
        }
      }
      if(type=='numberScale'){
        objx.minItem=this.kinds.numberScale.minItm
        objx.maxItem=this.kinds.numberScale.maxItm
        objx.header={}
      }
      if(type=='linearScale'){
        objx.pointerNumber=5
      }
      if(this.kinds[type].defMax!=null)
      {
        objx.max= this.kinds[type].defMax
      }
      if(this.kinds[type].min!=null)
      {
        objx.min= this.kinds[type].min
      }
      this.$emit('addItem',objx)
    }
  },
}
</script>