<template>
 <section  class="login-block">
    
    <div  class="container-fluid">
        <div  class="row">
            <div  class="col-sm-12">
                
                <form  class="md-float-material form-material">
                    <div  class="text-center">
                        <img  src="/images/logo.png" alt="logo.png"></img>
                    </div>
                    <div  class="auth-box card">
                        <div  class="card-block">
                            <div  class="row m-b-20">
                                <div  class="col-md-12">
                                    <h3  class="text-center txt-primary">ورود به حساب</h3>
                                </div>
                            </div>
                            <div  class="row m-b-20">
                                <div  class="col-md-6">
                                    <button  class="btn btn-facebook m-b-20 btn-block">
                                      <i  class="icofont icofont-social-facebook"></i>فیسبوک
                                    </button>
                                </div>
                                <div  class="col-md-6">
                                    <button  class="btn btn-twitter m-b-20 btn-block">
                                      <i  class="icofont icofont-social-telegram"></i>تلگرام
                                    </button>
                                </div>
                            </div>
                            <p  class="text-muted text-center p-b-5">ورود از طریق حساب کاربری</p>
                            <div  class="form-group form-primary">
                                <input  type="text" v-model="username" class="form-control" required=""/>
                                <span  class="form-bar"></span>
                                <label  class="float-label">نام کاربری</label>
                            </div>
                            <div  class="form-group form-primary">
                                <input  type="password" v-model="password" class="form-control" required=""/>
                                <span  class="form-bar"></span>
                                <label  class="float-label">رمز عبور</label>
                            </div>
                            <div  class="row m-t-25 text-left">
                                <div  class="col-12">
                                    <div  class="checkbox-fade fade-in-primary">
                                        <label > 
                                            <input  v-model="remember" type="checkbox" value=""/>
                                            <span  class="cr"><i  class="cr-icon icofont icofont-ui-check txt-primary"></i></span>
                                            <span  class="text-inverse">به خاطر بسپار</span>
                                        </label>
                                    </div>
                                    <div  class="forgot-phone text-right float-right">
                                        <a  href="/#/forget" class="text-right f-w-600">رمز عبور را فراموش کرده اید؟</a>
                                    </div>
                                </div>
                            </div>
                            <div  class="row m-t-30">
                                <div  class="col-md-12">
                                    <button  type="button" v-on:click="login()" class="btn btn-primary btn-md btn-block waves-effect text-center m-b-20">ورود</button>
                                    
                                </div>
                            </div>
                            <p  class="text-inverse text-left">حساب کاربری ندارید؟<a  href="/#/register"> <b >اینجا ثبت نام کنید </b>
                            </a></p>
                        </div>
                    </div>
                </form>
                    
                </div>
                
            </div>
            
        </div>
        
    
    
</section>
</template>
<script>
export default {
  name:"login",
   data:function()
      {
        return {
          username:"",
          password:"",
          remember:false,

        }
      },
      
    methods: { 
      getElementByRef:function(el){
        return this.$refs[el]
      },
      getMyParent:function(obj){return this.$parent},
      setState:function(obj){
        var al={}
        if(obj)
          for(var a in obj)
          { 
            try{
              this[a]=obj[a] 
            }catch(exp){}
          }
          
      },
      login:function (){  
            var dt =  httpPostData(global1.apiPath+'/v1/users/login',{phone:this.username,password:this.password,web:true})
            if(dt['x-auth-token'])
            {
                var time=1
                if(this.remember)
                    time=1200
                setCookie('token',dt['x-auth-token'],time)
                redirectTo('/#/home')
                return 
            }
            if(!dt.isDone)
            {
                return notify(viewShow(dt),"danger")
            }
                 
        },


    }
}
</script>