<template>
 <div  class="row">
    <div  class="col-sm-12">
    <div  class="card">
    
        <div  class="card-header">
            <h5 >{{title}}</h5>
           
        </div>
    
      
    <div  class="card-block">
     <AppTable  v-bind:url="url" v-bind:params="params" v-bind:reloading="reloadData" 
     
     v-bind:cols='[{"title":"باقی مانده","bind":"phone","ori-template":"que_remind"},{"title":"تعداد انجام شده","bind":"phone","ori-template":"que_number"},{"title":"تعداد سهمیه","bind":"phone","ori-template":"que_all"},{"title":"نام پروژه","bind":"ref","ori-template":"que_name"},{"title":"کد پروژه","bind":"id","name":"user_state"},
     {"title":"عملیات","ori-template":"que_opr"}]' >
     
     </AppTable>
    </div>
     
    
    <div  class="modal fade" id="queModal" tabindex="-1" role="dialog">
        <div  class="modal-dialog" role="document">
            <div  class="modal-content">
              <div  class="modal-header">
                <h4  class="modal-title">{{selectedQue.name}} </h4>
                <button  type="button" class="close" data-dismiss="modal" aria-label="Close">
                <span  aria-hidden="true">&times;</span>
                </button>
              </div>
              <div  class="modal-body" id="printData"> 
                <appViewQuestion  v-bind:selectedQue="selectedQue"></appViewQuestion>  
                
              </div>
              <div  class="modal-footer">
                <button  type="button" class="btn btn-default waves-effect " data-dismiss="modal">لغو</button>
                <button  v-on:click="saveUser()" type="button" class="btn btn-primary waves-effect waves-light ">تایید</button>
                <button  v-on:click="print()" type="button" class="btn btn-primary waves-effect waves-light ">پرینت</button>
              </div>
            </div>
        </div>
     </div>
    
</div>
</div> 




<div  class="modal fade" id="questionModal" tabindex="-1" role="dialog">
    <div  class="modal-dialog" role="document">
        <div  class="modal-content">
        <div  class="modal-header">
        <h4  class="modal-title">اخطار</h4>
        <button  type="button" class="close" data-dismiss="modal" aria-label="Close">
        <span  aria-hidden="true">&times;</span>
        </button>
        </div>
            <div  class="modal-body"> 
                
              <div  class="card-block">
  

                <div  class="form-group row">
                    <label  class="col-sm-8 col-form-label">آیا می خواهید پرسشنامه حذف شود؟
                        
                    </label> 
                </div> 

              </div>
            </div>
        <div  class="modal-footer">
        <button  type="button" class="btn btn-default waves-effect " data-dismiss="modal">لغو</button>
        <button  type="button" v-on:click="confirmDelete()" class="btn btn-primary waves-effect waves-light " data-dismiss="modal">تایید</button>
        </div>
        </div>
    </div>
 </div>

  <div  class="modal fade" id="dplModal" tabindex="-1" role="dialog">
    <div  class="modal-dialog" role="document">
        <div  class="modal-content">
        <div  class="modal-header">
        <h4  class="modal-title">اخطار</h4>
        <button  type="button" class="close" data-dismiss="modal" aria-label="Close">
        <span  aria-hidden="true">&times;</span>
        </button>
        </div>
            <div  class="modal-body"> 
                
              <div  class="card-block">
  

                <div  class="form-group row">
                    <label  class="col-sm-8 col-form-label"> آیا می خواهید یک نسخه کپی از این پرسشنامه ایجاد کنید؟
                        
                    </label> 
                </div> 
                <div  class="row">
                    <div  class="col-md-3">
                      <h5  class="card-title"> نام </h5>
                    </div>
                    <div  class="col-md-9">   
                      <input  type="text" v-model="newname"/>
                    </div>
                </div> 

              </div>
            </div>
        <div  class="modal-footer">
        <button  type="button" class="btn btn-default waves-effect " data-dismiss="modal">لغو</button>
        <button  type="button" v-on:click="confirmDuplicate()" class="btn btn-primary waves-effect waves-light " data-dismiss="modal">تایید</button>
        </div>
        </div>
    </div>
  </div>
  
  
  <div  class="modal fade" id="recoveryModal" tabindex="-1" role="dialog">
    <div  class="modal-dialog" role="document">
        <div  class="modal-content">
        <div  class="modal-header">
        <h4  class="modal-title">اخطار</h4>
        <button  type="button" class="close" data-dismiss="modal" aria-label="Close">
        <span  aria-hidden="true">&times;</span>
        </button>
        </div>
            <div  class="modal-body"> 
                
              <div  class="card-block">
  

                <div  class="form-group row">
                    <label  class="col-sm-12 col-form-label"> آیا مطمئن هستید، در صورت تایید پرسشنامه به صفحه پرسشنامه های پیشنویس ارسال شود؟
                        
                    </label> 
                </div>  

              </div>
            </div>
        <div  class="modal-footer">
        <button  type="button" class="btn btn-default waves-effect " data-dismiss="modal">لغو</button>
        <button  type="button" v-on:click="confirmRecovery()" class="btn btn-primary waves-effect waves-light " data-dismiss="modal">تایید</button>
        </div>
        </div>
    </div>
  </div>

</div>
</template>

<script>
import AppTable from '../global/appTable'
import que_remind from '../../templates/que_remind.vue'
import que_number from '../../templates/que_number.vue'
import que_all from '../../templates/que_all.vue'
import que_name from '../../templates/que_name.vue'
import que_opr from '../../templates/que_opr.vue'
import appViewQuestion from './appViewQuestion.vue'
export default {
  components:{
    AppTable,
    que_remind,
    que_number,
    que_all,
    que_name,
    que_opr,
    appViewQuestion,

  }, 
  name:'appShowQuestionnaire',
  
      data:function()
      {
        return {
          questionnaires:[],
          selectedQue:{},
          deleteId:"",
          recoverId:"",
          dplId:"",
          reloadData:0,
          newname:"",

        }
      },
      
    
    methods: { 
      getElementByRef:function(el){
        return this.$refs[el]
      },
      getMyParent:function(obj){return this.$parent},
      setState:function(obj){
        var al={"params":true,"url":true,"isDeleted":true,"title":true}
        if(obj)
          for(var a in obj)
          {
            //if(al[a])
            //  continue
            try{
              this[a]=obj[a] 
            }catch(exp){}
          }
          
      },
      print:function (){

      // var mywindow = window.open('', 'PRINT', 'height=400,width=600');
      // mywindow.document.write('<html><head><title>' + document.title  + '</title>');
      // mywindow.document.write('</head><body >');
      // mywindow.document.write('<h1>' + document.title  + '</h1>');
      // mywindow.document.write(document.getElementById('printData').innerHTML);
      // mywindow.document.write('</body></html>');
      // mywindow.document.close(); // necessary for IE >= 10
      // mywindow.focus(); // necessary for IE >= 10*/
  
      // mywindow.print();
      // mywindow.close();

      var divElements = document.getElementById('printData').innerHTML;
      var oldPage = document.body.innerHTML;
      document.body.innerHTML = 
        "<html><head><title></title></head><body>" + 
        divElements + "</body>";

      //Print Page
      window.print();

      document.body.innerHTML = oldPage;
      location.reload();
      //Restore orignal HTML

    },
    newPage:function (item){
      window.open(global1.apiPath.replace("8880","80") +"/#/home/"+item.id)
    },
        downloadexcell:function (item){//api.tickado.ir:8880/v1/panel/export2/:id
      window.open(global1.apiPath+"/v1/panel/export2/"+item.id)
    },
        viewQue:function (dt){ 
        this.setState({selectedQue:getCopy(dt)})
        $("#queModal").modal()
    },
        delete1:function (item){
        $("#questionModal").modal()  
        this.deleteId=item.id 
      
    },
        recovery:function (item){
        $("#recoveryModal").modal()  
        this.recoverId=item.id 
      
    },
        confirmRecovery:function (){
      
      var t= getCookie('token') 
      var dts=httpDeleteData(global1.apiPath+'/v1/q/'+this.recoverId,this.question,{'x-auth-token':t})
      this.reloadData++
    },
        confirmDelete:function (){
      
      var t= getCookie('token') 
      var dts=httpDeleteData(global1.apiPath+'/v1/q/'+this.deleteId,this.question,{'x-auth-token':t})
      this.reloadData++
    },
        duplicate:function (item){
      $("#dplModal").modal()    
        this.dplId=item.id 
        this.newname=item.name+'_Copy'

      
    },
        confirmDuplicate:function (){
      var t= getCookie('token') 
      var dts=httpGetData(global1.apiPath+'/v1/q/duplicate/'+this.dplId,{name:this.newname},{'x-auth-token':t})
      this.reloadData++

    },
        goto:function (item){ 
      window.location.href ='/#/home/questionnaire/'+item.id
         
    },
        
    },
    
    mounted:function (){  
      console.log('-----------------------------------global',
      global1)
    },

    props:{
        'params':{default:function(){ return []}},
        'url':{default:""},
        'isDeleted':{default:false},
        'title':{default:""},
        
      }, 
   

  

}
</script>