<template>
  <div> 
    <div  class="form-group row">
        <label  class="col-sm-2 col-form-label">نام</label>
        <div  class="col-sm-4">
            <input  v-model="question.name" type="text" class="form-control form-control-rtl" placeholder="سوال"/>
        </div> 
        <label  class="col-sm-2 col-form-label">زمان پاسخگویی</label>
        <div  class="col-sm-4">
            <input  v-model="question.duration" type="number" class="form-control form-control-rtl" placeholder="به دقیقه"/>
        </div>
    </div>  
    <div  class="form-group row">
        <label  class="col-sm-2 col-form-label">کارفرما</label>
        <div  class="col-sm-4">
                <select  v-model="question.empl" class="form-control">
                        <option  value="11"> کاله </option>
                        <option  value="12"> دامدارن </option>
                        <option  value="13"> پگاه </option>
                    </select> 
        </div> 
        <label  class="col-sm-2 col-form-label">تگ ها</label>
        <div  class="col-sm-4">
            <input-tag  type="text" v-model="question.tag" v-bind:add-tag-on-keys="tagData">
            </input-tag>
        </div>
    </div> 
    <div  class="form-group row">
        <label  class="col-sm-2 col-form-label">امتیاز</label>
        <div  class="col-sm-4">
            <input  v-model="question.point" type="number" class="form-control form-control-rtl" placeholder="میزان"/> 
        </div>
        <label  class="col-sm-2 col-form-label">شانس</label>
        <div  class="col-sm-4">
            <input  v-model="question.chance" type="number" class="form-control form-control-rtl" placeholder="تعداد"/>
              
        </div> 
    </div> 
    
    <div  class="form-group row">
        <div  class="col-sm-3"> 
            
                <div  class="checkbox-fade fade-in-primary">
                        <label > 
                            <input  v-model="question.isVoice" type="checkbox" value=""/>
                            <span  class="cr"><i  class="cr-icon icofont icofont-ui-check txt-primary"></i></span>
                            <span  class="text-inverse">ضبط داشته باشد</span>
                        </label>
                    </div>
              
        </div>  
        <div  class="col-sm-3"> 
            
                <div  class="checkbox-fade fade-in-primary">
                        <label > 
                            <input  v-model="question.isPublic" type="checkbox" value=""/>
                            <span  class="cr"><i  class="cr-icon icofont icofont-ui-check txt-primary"></i></span>
                            <span  class="text-inverse">عمومی باشد</span>
                        </label>
                    </div>
              
        </div>  
        <div  class="col-sm-3"> 
            
                <div  class="checkbox-fade fade-in-primary">
                        <label > 
                            <input  v-model="question.rotation" type="checkbox" value=""/>
                            <span  class="cr"><i  class="cr-icon icofont icofont-ui-check txt-primary"></i></span>
                            <span  class="text-inverse">چرخش داشته باشد</span>
                        </label>
                    </div>
              
        </div>  
        <div  class="col-sm-3"> 
            
                <div  class="checkbox-fade fade-in-primary">
                        <label > 
                            <input  v-model="question.isViewPaging" type="checkbox" value=""/>
                            <span  class="cr"><i  class="cr-icon icofont icofont-ui-check txt-primary"></i></span>
                            <span  class="text-inverse">تعداد سوالات را نمایش دهد؟</span>
                        </label>
                    </div>
              
        </div>  
    </div> 
    <div  class="row">
      <div  class="col-sm-12">
        <button  v-on:click="saveQuestion()" class="btn waves-effect waves-light btn-primary btn-outline-primary mfullwidth">
            ارسال</button>
      </div>  
    </div> 
  </div>
</template>

<script>
export default {
  name:"setting",
  data:function(){
    return {
      tagData:[13,9]
    };
  },
  props:{
    "question":{default:{}},
    "questionId":{default:""},
  },
  methods: {
    saveQuestion:function ()
    {
      var t= getCookie('token') 
      var q=getCopy(this.question) 
      var count=1
      var isd={};
      for(var a of q.questions)
      {
        if(a._id_ && !isd[a._id_])
        {
          isd[a._id_]=true;
        }
        else{
          a._id_=guid();
          isd[a._id_]=true;
        }
        a.number=count++ 
      }
      delete this.question._id
      this.question.id=this.questionId
      console.log(this.question)
      var dts=httpPutData(global1.apiPath+'/v1/q/'+this.question.id,q,{'x-auth-token':t}) 
      if(dts.id)
      { 
        this.$emit('update',this.question);
        return notify("عملیات انجام شد","success")
      }
      else
      {
        return notify(viewShow(dts),"danger")
      }
    },
  },
}
</script>