<template>
<div  class="table table-striped table-bordered nowrap">
  <div  class="table-responsive">
    <table  class="table">
      <thead >
        <tr >
          <th  v-if="rowCount">#</th>
          <th  v-for="(cl,index) in cols " :key="cl.id" >{{cl.title}}</th> 
        </tr>
      </thead>
      <tbody >
        <tr  v-for="(dts,index1) in data " :key="dts.id" >
          <th  v-if="rowCount" scope="row">{{index1+1}}</th>
            <td  v-for="(itm,index) in cols " :key="itm.id" >  
               
                <div  v-if="!itm.editable && itm.bind && !itm['ori-template'] ">
                  {{dts[itm.bind]}}

                </div>
                <div v-if="itm['ori-template']" :is="itm['ori-template']" :bindData="dts"></div>
            </td>           
        </tr> 
      </tbody>
    </table> 
    <appPagination  v-on:pageChane="pageChanged" v-bind:itemCount="count" v-bind:itemPerPage="top" v-bind:page="cur">
    </appPagination>
  </div>
</div>
</template>

<script> 
  import que_remind from '../../templates/que_remind.vue'
  import que_number from '../../templates/que_number.vue'
  import que_all from '../../templates/que_all.vue'
  import que_name from '../../templates/que_name.vue'
  import que_opr from '../../templates/que_opr.vue'
  import appPagination from './appPagination'
export default {

  components:{ 
    que_remind,
    que_number,
    que_all,
    que_name,
    que_opr,
    appPagination,
  }, 
  name:"AppTable",    
      data:function()
      {
        return {
          top:10,
          cur:0,
          tempreload:-1,

        }
      },
      
    
    methods: { 
      getElementByRef:function(el){
        return this.$refs[el]
      },
      getMyParent:function(obj){return this.$parent},
      setState:function(obj){
        var al={"reloading":true,"cols":true,"url":true,"header":true,"data":true,"rowCount":true,"count":true,"params":true}
        if(obj)
          for(var a in obj)
          {
            //if(al[a])
            //  continue
            try{
              this[a]=obj[a] 
            }catch(exp){}
          }
          
      },
      reload:function (){
      if(this.url)
      {
        var t= getCookie('token')
        var tempParam=getCopy(this.params)
        tempParam.pageNumber=this.cur+1
        tempParam.pageSize=this.top
        var dt=httpGetData(global1.apiPath+ this.url, tempParam ,{'x-auth-token':t})
      console.log('-----------------------------------global',dt)
        this.data=dt.value
        this.count=dt.count
        console.log('count is : ',this.count)
        this.setState({})
      }
    },
    pageChanged:function (data)
    {
      //console.log('-------->  ',data)
      this.cur=data-1 
      this.reload()
    },
        
    },
    
    updated:function (){
      if(this.tempreload!=this.reloading)
      {
        this.tempreload=this.reloading
        this.reload()

      }
    },
    mounted:function (){
      this.reload()
     
      //loadScripts(['files/assets/js/vertical/vertical-layout.min.js'])
    },

    props:{
        'reloading':{default:0},
        'cols':{default:function(){ return [{"title":"name","bind":"name"}]}},
        'url':{default:""},
        'header':{default:function(){ return {}}},
        'data':{default:function(){ return [{"name":"this name"}]}},
        'rowCount':{default:true},
        'count':{default:0},
        'params':{default:function(){ return {}}},
        
      },
}
</script>