<template>
  <div> 
    <div  class="row" >
        <div  class="col-sm-4" v-if="!item.isPicture || !item.media">
            <button  v-on:click="setIsPicture(true)" class="btn waves-effect waves-light btn-primary btn-outline-primary mfullwidth ">
                عکس</button>
        </div>  
        <div  class="col-sm-4" v-if="item.isPicture && item.media">
            <button  v-on:click="openUpload()" class="btn waves-effect waves-light btn-primary mfullwidth ">
                عکس</button>
        </div>  
        <div  class="col-sm-4" v-if="item.isPicture  || !item.media">
            <button  v-on:click="setIsPicture(false)" class="btn waves-effect waves-light btn-primary btn-outline-primary mfullwidth ">
                فیلم</button>
        </div>  
        <div  class="col-sm-4" v-if="!item.isPicture && item.media">
            <button  v-on:click="openUpload()" class="btn waves-effect waves-light btn-primary mfullwidth ">
                فیلم</button>
        </div>  
        <div  class="col-sm-4">
            <span >
                حد اکثر حجم:۲M</span>
        </div> 
    </div>   
    <div   class="row">
        <div  class="col-sm-12">
            <button  v-on:click="uploadData()" class="btn waves-effect waves-light btn-success  btn-outline-success  mfullwidth ">
                بارگذاری</button>
        </div>  
    </div> 
  </div>
</template>

<script>
export default {
  name:"uploader",
  props:{
    "item":{default:{}},
    "questionId":{default:""},
  },
  methods: {
    setIsPicture :function(isImg)
    {
      this.item.isPicture=isImg
      this.openUpload()
    },
    upload:function (){
      var el=document.getElementById('media') //this.getElementByRef('chartid')
        var fd = new FormData(); 
        fd.append("media", el.files[0]);

        var oReq = new XMLHttpRequest();
        var t= getCookie('token') 
        oReq.open("POST", global1.apiPath+"/v1/panel/upload/"+this.questionId, false);
        oReq.setRequestHeader('x-auth-token', t); 
        oReq.send(fd);
        return JSON.parse( oReq.responseText ); 
    },
    uploadData:function(){
      var data=this.upload()
      if(data.length)
      {
        this.$emit('uploaded',global1.apiPath+'/'+data[0].path) 
      }

    },
    setIsPictureQue:function (isImg){
      if(this.tmpWel)
      {
        this.tmpQue.isPicture=isImg
        this.openUpload() 
      }
    },
    openUpload:function (){

        $("#media").click();
       
    },
  },
}
</script>