<template>
  <div>
    
    <div  class="row">
        <div  class="col-md-12">


            <div  class=" card-sub" style="cursor: pointer;padding: 10px">

                <div  class="row">
                    <div  class="col-md-9">
                      <h5  class="card-title">صفحه خوش آمدید</h5>
                      <p  class="card-text" v-if="question.welcomePage">
                          {{question.welcomePage.title}}
                      </p>
                    </div>
                    <div  class="col-md-3">
                      <button  v-if="question.welcomePage" v-on:click="deleteWelcome()" class="btn waves-effect waves-light btn-danger btn-icon">
                        <i  class="fa fa-times"></i>
                      </button> 
                      <button  v-if="question.welcomePage" v-on:click="createWelcome()" class="btn waves-effect waves-light btn-primary btn-icon">
                        <i  class="fa fa-edit"></i>
                      </button> 
                    </div>
                </div>


            </div> 
        </div>     
    </div>  

      <div  class="row">

        <div  class="col-md-12" id="draggableMultiple">

            <div  v-for="(que,index) in question.questions " :key="que.id"  class="sortable-moves card-sub qitem" style="padding: 10px" v-bind:tagid="que.id">
              
              
              <div  class="row">
                  <div  class="col-md-9">
                    <h5  class="card-title">{{index+1}}</h5>
                    <div >
                      {{
                          convertToText(que.text)
                      }}
                    </div>
                    
                  </div>
                  <div  class="col-md-3">
                      <button  v-on:click="deleteQue(que)" class="btn waves-effect waves-light btn-danger btn-icon">
                        <i  class="fa fa-times"></i>
                      </button> 
                      <button  v-on:click="editQue(que)" class="btn waves-effect waves-light btn-primary btn-icon">
                        <i  class="fa fa-edit"></i>
                      </button> 
                      <button  v-on:click="dplQue(que,index)" class="btn waves-effect waves-light btn-primary btn-icon">
                        <i  class="fa fa-clone"></i>
                      </button> 
                  </div>
              </div>      
            </div>

        </div>

      </div>



      <div  class="row">
          <div  class="col-md-12">


              <div  class=" card-sub" style="cursor: pointer;padding: 10px">

                  <div  class="row">
                      <div  class="col-md-9">
                        <h5  class="card-title">صفحه تشکر</h5>
                        <p  class="card-text" v-if="question.endPage">
                            {{question.endPage.title}}
                        </p>
                      </div>
                      <div  class="col-md-3">
                        <button  v-if="question.endPage" v-on:click="deleteEnd()" class="btn waves-effect waves-light btn-danger btn-icon">
                          <i  class="fa fa-times"></i>
                        </button> 
                        <button  v-if="question.endPage" v-on:click="createWelcome(true)" class="btn waves-effect waves-light btn-primary btn-icon">
                          <i  class="fa fa-edit"></i>
                        </button> 
                      </div>
                  </div>


              </div> 
          </div>     
      </div> 


  </div>
</template>

<script>
export default {
  name:"questionView",
  data:function(){
    return {
      tagData:[13,9],
      changeMax:false,
      tmpQue:{},
      ssquestion:{}
    };
  },
  mounted:function() {
    
      $( function() {
        $( "#draggablePanelList,#draggableMultiple,#draggableWithoutImg" ).sortable({
          revert: true,
          animation:150
        });
      } );
  },
  methods: {
    editQue:function (q,tmp){
      this.changeMax=true
      // if(!tmp && this.tmpQue)
      // {
      //   var mx=this.tmpQue.answers.length 
      //   if(a.other=="true")
      //     mx+=this.tmpQue.otherNum
      //   if(mx != this.tmpQue.max)
      //     this.changeMax=false;
      // }
      // this.tmpQue={}
      // if(q && q.id)
      // {
      //   this.tmpQue=q
      // }
      this.change()
      // this.changedPage=true
      // this.reloadItems()
      this.$emit('edit',q)
    },
    // reloadItems:function (){
    //   if(! this.tmpQue || !this.tmpQue.id)
    //   {
    //     if(this.newItem.length>0)
    //       this.newItem=[]
    //     return
    //   }
    //   if(JSON.stringify(this.tmpQue)!=JSON.stringify(this.ssquestion))
    //   {
    //     this.ssquestion=getCopy(this.tmpQue)
    //     var item= this.reloadQuestion(this.question)
    //     this.newItem=[]
    //     for(var x in item[this.tmpQue.id+''])
    //     { 
    //       this.newItem.push(item[this.tmpQue.id+''][x])
    //     }
    //     console.log(this.newItem)
    //   }

    // },
    change:function (){ 
      var d=document.getElementsByClassName('sortable-moves')
      var obj=[]
      var tmp={}
      for(var x of d)
      {
        var id=  x.getAttribute('tagid')
        if(id==null)
          return
        for(var j of this.question.questions)
        {
          if(j.id==id)
          {
            if(tmp[j.id])
              break
            tmp[j.id]=true
            obj.push(j)
            break
          }
        }
        //console.log()
      }
      this.question.questions=obj
      this.$emit('update',this.question)
    },
    dplQue:function (q,indx)
    { 
      var cp=getCopy(q)
      cp.id=this.getNewNumber() 
      cp.number=this.getNewId() 
      this.question.questions.splice(indx+1,0,cp) // .push(cp)
        

    },
    getNewNumber:function (){
      var max=0
      for(var a of this.question.questions)
      {
        if(max<a.id)
        {
          max=a.id
        }
      }
      return max+1
    },
    getNewId:function (){
      var max=0
      for(var a of this.question.questions)
      {
        if(max<a.number)
        {
          max=a.number
        }
      }
      return max+1
    },
    deleteQue:function (q)
    {  
      if(!q || !q.id)
        return
      var dtx=this.question.questions  
      for(var a=0;a<dtx.length;a++)
      {
        var b=dtx[a]
        if(b.id==q.id)
        { 
          this.question.questions .splice(a,1)
        }
      }  
      this.$emit('update',this.question)

    },
    convertToText:function (data){
      return $(data).text()
    },
    deleteWelcome:function (){
      this.question.welcomePage=null  
      this.$emit('update',this.question)
    },
    deleteEnd:function (){
      this.question.endPage=null
      this.$emit('update',this.question)
    },
    createWelcome:function (isEnd){
      var tmpWel={
        viewUrl:false,
        isPicture:true,
        url:"",
        viewTitle:true,
        title:"",
        viewDescription:true,
        description:"",
        viewButtonTitle:true,
        buttonTitle:"",
      }
      if(isEnd){
        tmpWel.title="ارسال کنید"
        tmpWel.buttonTitle="ارسال" 
        if(this.question.endPage)
          tmpWel=getCopy(this.question.endPage)
          
        this.$emit('createEnd',tmpWel)
      }  
      else  
      { 
        tmpWel.buttonTitle="شروع" 
        if(this.question.welcomePage)
          tmpWel=getCopy(this.question.welcomePage)  
        this.$emit('createWelcome',tmpWel)

      }
      
    },
  },
  props:{
    "question":{default:{}},
    "questionId":{default:""},
  },
}
</script>