<template>
  <div>
  <div  style="display: none">
      {{ss}}
  </div> 
        <div  class="form-group row">
            <label  class="col-sm-3 col-form-label">گزینه ها</label> 
            

            <button  v-if="!tmpQue.answers.length" v-on:click="addAnsw(0)" class="btn waves-effect waves-light btn-primary btn-icon">
                <i  class="fa fa-plus"></i>
            </button>  

            <button  v-on:click="openExternal()" class="btn waves-effect waves-light btn-primary btn-icon">
                <i  class="fa fa-external-link"></i>
            </button>  
            <div  v-on:click="changeReq()" v-if="tmpQue.expected && !kinds[tmpQue.kind].itmForce && !tmpQue.isStar" style="font-size: 40px;color: red;">
                *
            </div>
            <div  v-on:click="changeReq()" v-if="!tmpQue.expected && !kinds[tmpQue.kind].itmForce" style="font-size: 40px;color: gray;">
                *
            </div>

            <div  v-on:click="changeExpectedAll(false)" v-if="isAllExpected() &&kinds[tmpQue.kind].itmForce && !tmpQue.isStar" style="font-size: 40px;color: red;">
                *
            </div>
            <div  v-on:click="changeExpectedAll(true)" v-if="!isAllExpected()&&kinds[tmpQue.kind].itmForce" style="font-size: 40px;color: gray;">
                *
            </div>

            <div  v-if="tmpQue.kind!='radio' && tmpQue.kind!='check'" class="label-icon" v-on:click="addExpectedCount()">
                <i  class="fa fa-star-o" style="font-size: 40px"></i>
                <label  v-if="!tmpQue.isStar" class="badge badge-primary badge-top-right">
                    0
                </label>
                <label  v-if="tmpQue.isStar" class="badge badge-primary badge-top-right">
                    1
                </label>
            </div>
<!-- 
            <div  v-if="tmpQue.kind!='radio' && tmpQue.kind!='check'"
              style="color: red;"
             class="label-icon" v-on:click="addCompleteStar()">
                <i  class="fa fa-star-o" style="font-size: 40px;margin-right: 30px;"></i>
                
                <label  v-if="tmpQue.isCompleteStar" class="badge badge-primary badge-top-right">
                    O
                </label>
            </div>
-->



        </div> 

        <div  v-for="(ans,index) in tmpQue.answers " :key="ans.id"  class="form-group row">
                
            <div  class="col-sm-1">
                
                <i  v-on:click="goUp(index)" class="badge bg-success"><i  class="fa fa-sort-asc"></i></i>
                <i  v-on:click="goDownp(index)" class="badge bg-success"><i  class="fa fa-sort-desc"></i></i>
            </div>  
            <div  class="col-sm-1" v-if="!tmpQue.isStar">

                    <div  v-on:click=" changeExpected(ans)" v-if="ans.expected && kinds[tmpQue.kind].itmForce" style="font-size: 40px;color: red;">
                    *
                    </div>
                    <div  v-on:click="changeExpected(ans)" v-if="!ans.expected &&  kinds[tmpQue.kind].itmForce" style="font-size: 40px;color: gray;">
                    *
                    </div>
            </div> 
            <div  class="col-sm-7">

                <input  v-model="tmpQue.answers[index].text" @input="$emit('update',tmpQue)" type="text" class="form-control form-control-rtl" placeholder="عنوان"/>
            </div>
            
            <div  class="col-sm-1">
                
                <i  v-on:click="deleteAnsw(index)" class="badge bg-danger"><i  class="fa fa-times"></i></i>
                <i  v-on:click="addAnsw(index)" class="badge bg-primary"><i  class="fa fa-plus"></i></i>
            </div>
            
            <div  class="col-sm-1">
                
                <i  v-on:click="showUploadModal(index)" class="badge bg-success"><i  class="fa fa-file-o"></i></i>
                <i  v-on:click="showSetting(index)" class="badge bg-success"><i  class="fa fa-cogs"></i></i>
            </div>

            <div  class="col-sm-11" v-if="ans.setting" style="border: 1px solid #ccc;border-radius: 3px;margin-top: 10px;margin-right: 20px;">
                <div  class="row">
 
                    <div  class="col-sm-12" v-if="kinds[tmpQue.kind].isdate">

                            <div  class="row">



                                <div  class="col-md-2">
                                        <div  class="checkbox-fade fade-in-primary">
                                            <label >  
                                                <span  class="text-inverse">روز </span>
                                            </label>
                                        </div>
                                    </div>
    
                                    <div  class="col-md-2"> 
                                        <input  class="js-single" v-model="tmpQue.answers[index].day" type="checkbox"/> 
                                    </div>



                                <div  class="col-md-2">
                                    <div  class="checkbox-fade fade-in-primary">
                                        <label >  
                                            <span  class="text-inverse">ماه </span>
                                        </label>
                                    </div>
                                </div>

                                <div  class="col-md-2"> 
                                    <input  class="js-single" v-model="tmpQue.answers[index].month" type="checkbox"/> 
                                </div>


                                
                                <div  class="col-md-2">
                                        <div  class="checkbox-fade fade-in-primary">
                                            <label >  
                                                <span  class="text-inverse">سال </span>
                                            </label>
                                        </div>
                                    </div>
    
                                    <div  class="col-md-2"> 
                                        <input  class="js-single" v-model="tmpQue.answers[index].year" type="checkbox"/> 
                                    </div>
                            </div>  

                    </div>
                    <div  class="col-sm-12" v-if="kinds[tmpQue.kind].itmMax">
                        

                        <input  v-model="tmpQue.answers[index].max" type="number" class="form-control form-control-rtl" placeholder="کمترین مقدار"/>
                    </div>
                    <div  class="col-sm-12" v-if="kinds[tmpQue.kind].itmMin">

                        <input  v-model="tmpQue.answers[index].min" type="number" class="form-control form-control-rtl" placeholder="بیشترین مقدار"/>
                    </div>

                </div>
            </div>

            
        </div>
        
        <div  class="row" v-for="(ans,index) in tmpQue.external " :key="ans.id" >

            

                <div  class="col-sm-2">
                   <div v-if="ans.isStar"
                      style="color: red;"
                      class="label-icon" @click="tmpQue.external[index].isStar=!tmpQue.external[index].isStar;ss++">
                        <i  class="fa fa-star-o" style="font-size: 40px;margin-right: 10px;"></i>
                        
                    </div>
                    <div v-if="!ans.isStar" 
                      class="label-icon" @click="tmpQue.external[index].isStar=!tmpQue.external[index].isStar;ss++">
                        <i  class="fa fa-star-o" style="font-size: 40px;margin-right: 10px;"></i>
                        
                    </div>
                </div>
                <div  class="col-sm-10">
                    <span  class="form-control form-control-rtl">
                        {{convertToText(ans.text)  +' ('+selectToName(ans.type)+')'}} 
                    </span>
                </div>
        </div>
  </div>
</template>

<script>
export default {
  name:"options",
  data:function(){
      return {
          ss:0,
          kinds:{"singleLine":{"min":null,"max":100,"defMax":20,"showMinMax":true,"viewRotate":false,"itmForce":true,"place":true},"decimal":{"min":"","max":"","defMax":"","showMinMax":false,"viewRotate":false,"itmMin":true,"itmMax":true,"itmForce":true,"place":true},"int":{"min":"","max":"","defMax":"","showMinMax":false,"viewRotate":false,"itmMin":true,"itmMax":true,"itmForce":true,"place":true},"multiLine":{"min":null,"max":50,"defMax":500,"showMinMax":true,"viewRotate":false,"itmForce":true,"place":true},"linearScale":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":true,"itmMin":true,"itmMax":true,"itmForce":true,"pNum":true,"pType":"fa-star-o","pDef":6},"numberScale":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":true,"itmMin":true,"itmMax":true,"itmForce":true,"minItm":1,"maxItm":5},"radio":{"min":0,"max":4,"defMax":3,"showMinMax":false,"viewRotate":true,"AutoNext":true},"check":{"min":0,"max":null,"defMax":1,"showMinMax":true,"viewRotate":true},"email":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":false,"itmForce":true,"place":true},"range":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":true,"itmMin":true,"itmMax":true,"itmForce":true},"link":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":false,"itmForce":true,"place":true},"priority":{"min":0,"max":null,"defMax":1,"showMinMax":true,"viewRotate":true},
          
          "page":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":false},
          "phone":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":false},
          
          "file":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":false,"itmMin":true,"itmMax":true,"itmForce":true},"dateTime":{"min":0,"max":100,"defMax":20,"showMinMax":false,"viewRotate":false,"itmForce":true,"isdate":true}},

      }
  },
  props:{
      "tmpQue":{default:{}}
  },
  methods: {
    openExternal:function(){
      
      $("#editModal").modal()
    },
    addExpectedCount:function (){
      this.tmpQue.isStar=!this.tmpQue.isStar 
      this.ss++ 
    },
    addCompleteStar:function (){
      this.tmpQue.isCompleteStar=!this.tmpQue.isCompleteStar 
      this.ss++ 
    },
    getNewAnswId:function (){
      var max=0
      if(Array.isArray(this.tmpQue.answers))
        for(var a of this.tmpQue.answers)
        {
          if(max<a.id)
          {
            max=a.id
          }
        }
      return max+1
    },
    deleteAnsw:function (ans){ 
      if(ans>=0)
      {
        this.tmpQue.answers.splice(ans,1)
        this.changeMax=true 
      this.$emit('update',this.tmpQue)
      } 
    },
    addAnsw:function (index){
      var answs={id:this.getNewAnswId(),text:""} 
      var type=this.tmpQue.kind[0]
      if(type=="dateTime")
      {
        answs.year=true
        answs.month=true
        answs.day=true
        answs.setting=true
      }
      if(this.kinds[type].pNum )
      {
        answs.type= this.kinds[type].pType
        answs.pnum= this.kinds[type].pDef
      }
      this.tmpQue.answers.splice(index+1,0,answs)
      this.changeMax=true 
      this.$emit('update',this.tmpQue)
    },
    showSetting:function (ans){ 
        this.ss++
      if(ans>=0)
      {
          console.log('-----',this.tmpQue.answers[ans])
        this.tmpQue.answers[ans].setting =!this.tmpQue.answers[ans].setting 
      } 
    },
    isAllExpected:function ()
    {
      var t=true
      for(var a of this.tmpQue.answers)
      {
        if(!a.expected)
          t=false
      }
      return t
    },
    changeExpected:function(ans){
      this.tmpQue.isStar=false
      ans.expected=!ans.expected
      this.ss++ 
    },  
    changeExpectedAll:function (val){
      this.tmpQue.isStar=false
       for(var a of this.tmpQue.answers)
       {
         a.expected=val
       }
       this.$emit('update',this.tmpQue)
    },
    changeReq:function (){ 
      this.tmpQue.isStar=false
      this.tmpQue.expected=!this.tmpQue.expected
      if(this.tmpQue.expected)
        this.tmpQue.min=1 
      else
        this.tmpQue.min=0  
        
       this.$emit('update',this.tmpQue)
    },
    convertToText:function (data){
      return $(data).text()
    },
    selectToName:function (name){
      if(name=="all")
      {
        return "همه"
      }
      if(name=="selected")
      {
        return "انتخاب شده " 
      }
      if(name=="notSelected")
      {
        return "انتخاب نشده" 
      }
      return ""
    },
  },
}
</script>