<template>
  <div>
    <div style="display:none">
    {{counter}}
    </div>
    <div  class="row">
        <button  v-on:click="addconditions()" class="col-md-1 btn waves-effect waves-light btn-primary btn-outline-primary ">
            <i  class="fa fa-plus">شرط جدید</i>
        </button> 
      </div>
    <div  class="row" >

        <div  class="col-md-12">

            <div  v-for="(que,index) in question.conditions " :key="que.id"  class="  card-sub" style="padding: 10px" v-bind:tagid="que.id">
                        <button  v-on:click="que.isopen=!que.isopen;counter++" class="btn waves-effect waves-light btn-priority btn-icon">
                            <i v-if="que.isopen" class="fa fa-times"></i>
                            <i v-if="!que.isopen" class="fa fa-eye"></i>
                        </button> 
                        {{index+1}}
                <input v-model="que.description" />
                
                <div  class="row" v-if="que.isopen">
                    <div  class="col-md-11">  
                            <condition  v-bind:data="que.subCond" v-bind:question="tmpQuestion" v-bind:myAnsw="questionAnsw">
                            </condition>
                    </div>
                    
                    <div  class="col-md-1">
                        <button  v-on:click="deleteCond(que)" class="btn waves-effect waves-light btn-danger btn-icon">
                            <i  class="fa fa-times"></i>
                        </button>  

                        <button  v-on:click="dplCond(que)" class="btn waves-effect waves-light btn-priority btn-icon">
                            <i  class="fa fa-plus"></i>
                        </button>  
                    </div>
                </div>        
                <div v-if="que.isopen"> 
                <div class="row" >
                
                    <div  class="col-sm-1 col-form-label"> 
                            پرش به 
                    
                    </div>
                    <div  class="col-md-3" > 
                            <select  v-model="que.goTo" class="form-control">
                                <option  v-bind:value="1"> ادامه بده </option>
                                <option  v-bind:value="2"> برو به صفحه ارسال </option>
                            </select> 
                    </div>
                </div>
                  <div  class="row" v-for="(ques,index2) in que.questions " :key="ques.id"  >
                      <div  class="col-sm-1 col-form-label"> 
                          <span  v-if="index2!=0">
                              و
                          </span>
                      </div>
                      <div  class="col-sm-1 col-form-label"> 
                              باشد سوال 
                      
                      </div>
                      <div  class="col-md-3"> 
                              <select  v-model="question.conditions[index].questions[index2].questionId" class="form-control">
                                  <option  v-for="(itm,index3) in question.questions " :key="itm._id_"  v-bind:value="itm._id_">
                                      {{ getText( (index3+1)+' - '+ convertToText(itm.text))  }}
                                  </option>
                              </select> 
                      </div>
                      
                      <div  class="col-sm-1 col-form-label"> 
                              را
                      
                      </div>
                      <div  class="col-md-3"> 
                              <select  v-model="ques.show" class="form-control">
                                  <option  v-bind:value="true"> نمایش بده </option>
                                  <option  v-bind:value="false"> نمایش نده </option>
                              </select> 
                      </div>
                      <div  class="col-md-3">
                              <button  v-if="index2!=0" v-on:click="deleteCondtitionShow(index,index2)" class="btn waves-effect waves-light btn-danger btn-icon">
                                  <i  class="fa fa-times"></i>
                              </button>  

                              <button  v-on:click="dplCondtitionShow(index,index2)" class="btn waves-effect waves-light btn-priority btn-icon">
                                  <i  class="fa fa-plus"></i>
                              </button>  
                          </div>
                  </div>
                </div>
            </div>

        </div>

    </div>
    
  </div>
</template>

<script>
import condition from './condition'
export default {
  name:"conditionView",
  components:{
    condition
  },
  data:function()
  {
    return {
      isopen:false,
      counter:0
    };
  },
  props:{
    "question":{default:{}},
    "tmpQuestion":{default:{}},
    "questionAnsw":{default:{}},
  },
  mounted:function () {
  console.log('---------->>->',this.question.conditions)
    for(var a of this.question.conditions)
    {
      a.isopen=false;
    }
  },
  methods: {
    addconditions:function (){ 
      this.question.conditions.push({
        id:guid(),
        subCond:{
         "type":"and",
         "children":[ 
         ]
        }, 
        questions:[
          {
            "_id_":Math.random(),
            "questionId":"",
            "show":""
          }
        ]
       }) 
    }, 
    getText:function (text){
      if(text.length>80)
        text=text.substr(0,80)+'...'
        return text
    },
    convertToText:function (data){
      return $(data).text()
    },
    deleteCondtitionShow:function (ind,ind1){ 
    console.log('delete condition',ind,ind1)
      this.question.conditions[ind].questions.splice(ind1,1) 
    },
    dplCondtitionShow:function (ind,ind1){ 
      this.question.conditions[ind].questions.splice(ind1+1,0,{questionId:"",show:""}) 
    },
    dplCond:function(q)
    { 
      var cp=getCopy(q)
      for(var ss of cp.questions)
      {
        ss._id_=Math.random();
      }
      cp.id=guid()
      this.question.conditions.push(cp)
    },
    deleteCond:function(q)
    {
      console.log('----------------------->',q)
      for(var a =0;a<this.question.conditions.length;a++)
      {
        var dt=this.question.conditions[a];
        if(q.id==dt.id)
        {
          this.question.conditions.splice(a,1)
          break;
        }
      } 

    }
  },
}
</script>