<template>
  <div  itemName="item" name="que_remind">
    <div  v-if="item">              
      <div  v-if="item.quota && item.quota.capacityRemained">
                      {{ item.quota.capacityRemained }}              
      </div>            
    </div>          
  </div>
</template>

<script>
export default {
  name:"que_remind",
  props:{"itemName":{"default":"item"},"name":{"default":"que_remind"},"bindData":{}}
  ,data:function(){return {"item":{},"que_remind":{}} }
  ,mounted:function(){this[this.itemName]=this.bindData }
}
</script>

