<template>
  <div>
    <div  class="row">
        <button  v-on:click="addconditions()" class="col-md-1 btn waves-effect waves-light btn-primary btn-outline-primary ">
            <i  class="fa fa-plus"> سهمیه جدید</i>
        </button> 
      </div> 
      <div v-for="(itm,index) in qutas.children" class="row" style="border-bottom-style: dotted;">
        <div class="col-8">  
          <subQuta :data="itm">
          </subQuta>
        </div>  
        <div class="col-4">  
          <div style="height:70px">
          </div>
                        <button  v-on:click="deleteQuta(index)" class="btn waves-effect waves-light btn-danger btn-icon">
                            <i  class="fa fa-times"></i>
                        </button>  
        </div>  
      </div>  
  </div>
</template>

<script>
import subQuta from './subQuta'
export default {
  name:"localQuta",
  components:{
    subQuta,
  },
  props:{
    qutas:{default:{children:[]}}
  },
  methods: {
    deleteQuta(index){
      this.qutas.children.splice(index,1)
    },
    addconditions(){
      this.qutas.children.push({});
    }
  },
}
</script>